import { Component, ViewChild, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { List, Map, fromJS } from 'immutable';

import { GSPEnumValue, ViewElement, ViewEnumValueList } from '../../util/vm-reference';
import { DataBindingDirective, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';

@Component({
    selector: 'app-vm-enum-edit',
    templateUrl: './enum-edit.component.html',
    styleUrls: ['./enum-edit.component.css']
})
export class EnumEditComponent implements OnChanges {

    @ViewChild('grid') grid: GridComponent;
    /**
     * 是否显示枚举弹框
     */
    public active = false;

    /**
     * 当前字段
     * 用来加载和确认
     */
    @Input() viewElement: ViewElement;
    @Input() canEdit: boolean;

    /**
     * 枚举弹框数据源
     * 用来保存过程中编辑数据
     */
    private enumValues: GSPEnumValue[];
    public seletedKeys: any[] = [];
    public selectableSettings: SelectableSettings = {
        checkboxOnly: false,
        mode: 'single'
    };

    constructor(private formBuilder: FormBuilder) {
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.load(this.viewElement);
    }

    /**
     * 根据传入的字段，加载枚举弹窗
     * @param viewEle
     */
    load(viewEle: ViewElement) {
        this.active = true;
        this.viewElement = viewEle;
        if (this.viewElement && this.viewElement.enumValues) {
            this.enumValues = this.viewElement.enumValues.toJS();
        }
    }

    /**
     * 确认
     */
    onConfirm(): void {
        // 当且仅当确认的时候，需要正在编辑的数据，故仅在confirm中调用closeCell
        this.grid.closeCell();
        this.viewElement.enumValues = new ViewEnumValueList(this.enumValues);
    }

    /**
     * 保存前检查
     */
    checkBeforeSave(): boolean {
        // ① 空，则通过；
        if (isNullOrUndefined(this.enumValues) || this.enumValues.length === 0) {
            alert('请添加枚举值。');
            return false;
        }
        // ② 非空，则校验每个的键值是否为空；
        for (const item of this.enumValues) {
            if (!item.Name || !item.Value) {
                alert('枚举键或值均不允许为空。');
                return false;
            }
        }
        // ③ 多于一个，键不允许重复；
        if (this.enumValues.length > 1) {
            for (let i = 0; i < this.enumValues.length; i++) {
                const key1 = this.enumValues[i].Value;
                for (let j = i + 1; j < this.enumValues.length; j++) {
                    const key2 = this.enumValues[j].Value;
                    if (key1 === key2) {
                        alert('枚举的键不允许重复。');
                        return false;
                    }
                }
            }
        }
        // ③ 多于一个，值不允许重复；
        if (this.enumValues.length > 1) {
            for (let i = 0; i < this.enumValues.length; i++) {
                const name1 = this.enumValues[i].Name;
                for (let j = i + 1; j < this.enumValues.length; j++) {
                    const name2 = this.enumValues[j].Name;
                    if (name1 === name2) {
                        alert('枚举的值不允许重复。');
                        return false;
                    }
                }
            }
        }
        return true;
    }

    /**
     * 取消
     */
    onCancel(): void {
        this.close();
    }

    close(): void {
        this.active = false;
    }

    /**
     * 开始编辑
     * @param param0
     */
    private cellClickHandler({ sender, rowIndex, columnIndex, dataItem, isEdited }) {
        if (!this.canEdit) {
            return;
        }
        if (!isEdited) {
            sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
        }
    }

    /**
     * 结束编辑
     * @param args
     */
    private cellCloseHandler(args: any) {
        const { formGroup, sender, rowIndex, columnIndex, dataItem, isEdited, column } = args;

        // if (!formGroup.valid) {
        //     args.preventDefault();
        // } else if (formGroup.dirty) {
        const propertyName = column.field;
        const newValue = formGroup.value[propertyName];
        switch (column.field) {
            case 'Name':
                this.enumValues[rowIndex].Name = newValue;
                break;
            case 'Value':
                this.enumValues[rowIndex].Value = newValue;
                break;
        }
        // }
        sender.cancelCell();
    }

    /**
     * 新增枚举值
     * @param param0
     */
    private addHandler() {
        // sender.addRow(this.createFormGroup(new GSPEnumValue()));
        if (isNullOrUndefined(this.enumValues)) {
            this.enumValues = new Array<GSPEnumValue>();
        }
        this.enumValues.push(new GSPEnumValue());
    }

    /**
     * 新增后的行按钮（确认）
     */
    private saveHandler({ sender, formGroup, rowIndex, dataItem }) {
        if (formGroup.valid) {
            if (isNullOrUndefined(this.enumValues)) {
                this.enumValues = new Array<GSPEnumValue>();
            }
            this.enumValues.unshift(formGroup.value);
            sender.closeRow(rowIndex);
        }
    }

    /**
     * 新增后的行按钮（取消）
     */
    private cancelHandler({ sender, rowIndex }) {
        sender.closeRow(rowIndex);
    }

    /**
     * 删除
     * @param args
     */
    private removeHandler(): void {
        if (isNullOrUndefined(this.seletedKeys) || this.seletedKeys.length === 0) {
            alert('请选中要删除的枚举。');
            return;
        }
        const rowIndex = this.seletedKeys[0];
        this.enumValues.splice(rowIndex, 1);
    }

    private createFormGroup(dataItem: any): FormGroup {
        return this.formBuilder.group({
            'Value': [dataItem.Value, Validators.required],
            'Name': [dataItem.Name, Validators.required],
        });
    }
}



