import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ModalModule } from '@farris/ui/container/modal/modal';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { TreeModule, TreeTableModule } from 'primeng/primeng';
import { GridModule } from '@progress/kendo-angular-grid';
import { InputTextModule } from 'primeng/inputtext';
import { MetadataService } from '@gsp-lcm/metadata-selector/metadata/metadata-select/metadata.service';
import { FarrisDialogModule } from '@farris/ui';
import { ObjectTypeEditorInVmComponent } from './object-type-editor/object-type-editor-in-vm.component';
import { AssociationEditComponent } from '../../element-collection/association-edit/association-edit.component';
import { EnumEditComponent } from '../../element-collection/enum-edit/enum-edit.component';
import { AssoKeyEditFormComponent } from '../../element-collection/association-edit/association-edit-form/association-edit-form.component';
import {
  AssociationElementGirdComponent
} from '../../element-collection/association-edit/association-element-gird/association-element-gird.component';
import {
  AssociationRefElementGirdComponent
} from '../../element-collection/association-edit/association-refElement-grid/association-refElement-grid.component';
import {
  GSPElementDataTypeConvert, GSPElementObjectTypeConvert, GSPAssociationModelNames, GSPEnumValues
} from '../../util/ele-collection-util';
import { AssociationShowGridComponent } from '../../element-collection/association-show-grid/association-show-grid.component';
import { ObjectTypeEditorComponent } from '../../element-collection/object-type-editor/object-type-editor.component';
import { MetadataModule } from '../metadata/metadata.module';
import { ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';

@NgModule({
  imports: [
    CommonModule,
    ModalModule.forRoot(),
    FormsModule,
    TreeViewModule,
    TreeModule,
    TreeTableModule,
    GridModule,
    InputTextModule,
    FarrisDialogModule,
    MetadataModule,
    ButtonsModule,
    ButtonGroupModule,
    FormsModule,
    ReactiveFormsModule,
  ],
  exports: [
    ObjectTypeEditorInVmComponent
  ],
  providers: [MetadataService], // 服务提供者
  declarations: [
    ObjectTypeEditorInVmComponent,

    // 字段枚举编辑器
    AssociationEditComponent,
    EnumEditComponent,
    AssoKeyEditFormComponent,
    AssociationElementGirdComponent,
    AssociationRefElementGirdComponent,
    // GSPElementDataTypeConvert,
    // GSPElementObjectTypeConvert,
    GSPAssociationModelNames,
    GSPEnumValues,
    // AssociationShowGridComponent,
    ObjectTypeEditorComponent],
  entryComponents: [ObjectTypeEditorComponent]
})
export class ObjectTypeEditorModule { }



