import { NgModule } from '@angular/core';

import { LayoutModule, SplitterModule } from '@progress/kendo-angular-layout';

import { SecurityEntryModule } from './security-entry/security-entry.modue';
import { FuncOpTabComponent } from './func-op-tab/func-op-tab.component';
import { AuthOpTabComponent } from './auth-op-tab/auth-op-tab.component';
import { AuthTabComponent } from './auth-tab.component';


@NgModule({
  imports: [
    SecurityEntryModule,
    LayoutModule,
    SplitterModule
  ],
  entryComponents: [FuncOpTabComponent, AuthOpTabComponent, AuthTabComponent],
  declarations: [
    FuncOpTabComponent,
    AuthOpTabComponent,
    AuthTabComponent
     ],
  exports: [FuncOpTabComponent, AuthOpTabComponent, AuthTabComponent],
  providers: []
})

export class AuthTabModule { }



