import {
  Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter,
  SimpleChanges, Renderer2, Injector, ComponentFactoryResolver
} from '@angular/core';
import { FormArray, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

import { AddEvent, EditEvent, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';

import { ModalDirective } from '@farris/ui/container/modal/modal/modal.directive';
import { BsModalService } from '@farris/ui';

import { FuncOperationEntity } from '../entity/func-operation.entity';
import { SecurityEntryEntity } from '../entity/security-entry.entity';
import { OpRelationEntity } from '../entity/op-relatioin.entity';

import { SecurityEntryService } from '../service/security-entry.service';
import { isNullOrUndefined } from 'util';

import { AuthFieldGridComponent } from './auth-field-grid/auth-field-grid.component';
// import { AuthOpGridComponent } from './auth-op-grid/auth-op-grid.component';

import { AuthorizationEntity } from '../../authorization/entity/authorizationentity';
import { AuthOpRelationEntity } from '../entity/auth-op-relation.entity';
import { AuthFieldEntryEntity } from '../entity/auth-field-entry.entity';

@Component({
  selector: 'app-security-entry-auth-op',
  templateUrl: './security-entry-auth-op.component.html',
  styleUrls: ['./security-entry-auth-op.component.css']
})
export class SecurityEntryAuthOpComponent implements OnInit, OnChanges {

  @ViewChild('fieldGrid') fieldGrid: AuthFieldGridComponent;
  // @ViewChild('opGrid') opGrid: AuthOpGridComponent;


  @Input() actions: Array<any> = [];
  @Input() bizfields: Array<any> = [];
  @Input() securityEntry: SecurityEntryEntity;

  authorizationNames: string;

  isNew = true;

  showAuthorizationWindow = false;

  saveEmitter: EventEmitter<SecurityEntryEntity> = new EventEmitter<SecurityEntryEntity>();

  constructor(private securityEntryService: SecurityEntryService) { }
  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.load();
  }

  load() {
    if (isNullOrUndefined(this.securityEntry)) {
      return;
    }

    if (this.securityEntry && this.securityEntry.authFields) {
      this.buildData();
      return; // 第一次加载界面时，为undefined，加载一次赋值后，这里就不会出现undefined的情况了
    }

    this.securityEntryService.getAuthSecurityEntry(this.securityEntry).subscribe(item => {
      if (this.securityEntry) {
        if (item) {
          this.securityEntry.id = item.id; // 如果场景已经存在，则将获取场景ID
          this.securityEntry.authObjIds = item.authObjIds;
          this.securityEntry.authFields = item.authFields;
        } else {
          if (isNullOrUndefined(this.securityEntry.authObjIds)) {
            this.securityEntry.authObjIds = '';
          }
          if (isNullOrUndefined(this.securityEntry.authFields)) {
            this.securityEntry.authFields = [];
          }
        }
        this.buildData();
        this.authorizationNames = this.securityEntry.authObjNames;
      }
    });
  }

  buildData() {
    this.bizfields.forEach(itemField => {
      const item = this.securityEntry.authFields.find(entry => entry.fieldId === itemField.id);
      if (item) {
        item.fieldCode = itemField.code;
        item.fieldName = itemField.name;
      } else {
        const newRelation = new AuthFieldEntryEntity();
        newRelation.fieldId = itemField.id;
        newRelation.fieldName = itemField.name;
        this.securityEntry.authFields.push(newRelation);
      }
    });
    this.fieldGrid.datas = this.securityEntry.authFields;
  }

  /**
   * 获取[权限对象]显示内容
   */
  get authorizationsDisplay(): string {
    if (this.securityEntry.authObjNames) {
      return this.securityEntry.authObjNames;
    }
    return '';
  }

  /**
 * 弹出[权限对象]选择界面，根据勾选情况重置权限对象
 */
  selectAuthorizations() {
    this.showAuthorizationWindow = !this.showAuthorizationWindow;
  }

  // 弹出[权限对象]选择界面时，传入已经选中的权限对象
  get selectedAuthIds(): string[] {
    const authobjs = this.securityEntry.authObjIds;
    if (isNullOrUndefined(authobjs)) {
      return null;
    }
    const authorizationIds = new Array<string>();
    return authobjs.split(',');
  }

  // 权限对象选择框 关闭事件
  getAuthorizations(auths: any) {
    if (isNullOrUndefined(auths) || auths.length === 0) {
      this.securityEntry.authObjIds = '';
      return;
    } else {
      const authInfos = new Array<AuthorizationEntity>();
      const authobjIds = new Array<string>();
      const authobjNames = new Array<string>();
      this.securityEntry.authObjIds = '';
      auths.forEach(auth => {
        const authInfo = new AuthorizationEntity();
        authInfo.id = auth.id;
        authInfo.name = auth.name;
        authInfos.push(authInfo);
        authobjIds.push(auth.id);
        authobjNames.push(auth.name);
      });

      let models = '';
      authobjIds.forEach(id => {
        models += id + ',';
      });
      this.securityEntry.authObjIds = models.slice(0, models.length - 1);
      models = '';
      authobjNames.forEach(name => {
        models += name + ',';
      });
      this.securityEntry.authObjNames = models.slice(0, models.length - 1);
      this.authorizationNames = this.securityEntry.authObjNames;
      return;
    }
  }


  /***** 用于数据操作 选择组件的输入 Start*****/
  get authFieldEntryEntity(): AuthFieldEntryEntity {
    return this.fieldGrid.currentAuthFieldEntry;
  }

  get authorizationId(): string {
    if (this.fieldGrid.currentAuthFieldEntry) {
      return this.fieldGrid.currentAuthFieldEntry.authObjId;
    }
    return '';
  }

  get authfield(): string {
    if (this.fieldGrid.currentAuthFieldEntry) {
      return this.fieldGrid.currentAuthFieldEntry.authFieldId;
    }
    return '';
  }
  /***** 用于数据操作 选择组件的输入 End*****/


  /**
  * 保存权限扩展信息
  *
  */
  save() {
    this.securityEntryService.saveAuthSecurityEntry(this.securityEntry).subscribe(item => {
      this.securityEntry.id = item.id;
      this.saveEmitter.next(this.securityEntry);
    });
  }
}



