import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ViewParameterListBase, ViewBizActionParaList, ViewCdpActionParaList } from './viewParameters/viewParameterList';
import { ViewReturnValue } from './viewParameters/viewReturnValue';

import {
    AuthType, ViewModelActionType,
    ViewOperationConst, ViewModelAction, MappedBizAction, MappedCdpAction, ViewModelReturnValue
} from '../../ref';
import { ViewMapping } from '../viewMapping/viewMapping';

export abstract class ViewActionBase extends MapBase {

    constructor(action: ViewModelAction | Map<string, any>) {
        super(action);
        this.update(ViewOperationConst.OpIdList, values => this.getOpIdList(values));
        this.update(ViewOperationConst.ParameterCollection, values => this.getParameterList(values));
        this.update(ViewOperationConst.ReturnValue, value => this.getViewReturnValue(value));
        this.update(ViewOperationConst.Mapping, mapping => this.getViewMapping(mapping));
    }

    private getViewMapping(mapping: Map<string, any>) {
        if (isNullOrUndefined(mapping)) {
            return;
        }
        const viewValueHelpConfigs = new ViewMapping(mapping);
        return viewValueHelpConfigs;
    }

    private getOpIdList(opIds: List<string>): ListBase<MapBase> {
        return new ListBase<MapBase>(opIds);
    }

    protected abstract getParameterList(paras: List<any>): ViewParameterListBase;

    getViewReturnValue(value: ViewModelReturnValue): any {
        return new ViewReturnValue(value);
    }

    get type(): ViewModelActionType {
        return this.get(ViewOperationConst.Type);
    }
    set type(value: ViewModelActionType) {
        this.set(ViewOperationConst.Type, value);
    }

    get OpIdList(): ListBase<MapBase> {
        return this.get(ViewOperationConst.OpIdList);
    }
    set OpIdList(value: ListBase<MapBase>) {
        this.set(ViewOperationConst.OpIdList, value);
    }

    get ReturnValue(): ViewReturnValue {
        const returnValue = this.get(ViewOperationConst.ReturnValue);
        // if (isNullOrUndefined(returnValue) || isNullOrUndefined(returnValue.toJS())) {
        //     this.set(ViewOperationConst.ReturnValue, new ViewReturnValue(new BizVoidReturnType()));
        // }
        return returnValue;
    }
    set ReturnValue(value: ViewReturnValue) {
        this.set(ViewOperationConst.ReturnValue, value);
    }

    get ComponentId(): string {
        return this.get(ViewOperationConst.ComponentId);
    }
    set ComponentId(value: string) {
        this.set(ViewOperationConst.ComponentId, value);
    }

    get ID(): string {
        return this.get(ViewOperationConst.ID);
    }

    get Code(): string {
        return this.get(ViewOperationConst.Code);
    }
    set Code(value: string) {
        this.set(ViewOperationConst.Code, value);
    }

    get Name(): string {
        return this.get(ViewOperationConst.Name);
    }
    set Name(value: string) {
        this.set(ViewOperationConst.Name, value);
    }

    get Description(): string {
        return this.get(ViewOperationConst.Description);
    }
    set Description(value: string) {
        this.set(ViewOperationConst.Description, value);
    }

    get IsVisible(): boolean {
        return this.get(ViewOperationConst.IsVisible);
    }
    set IsVisible(value: boolean) {
        this.set(ViewOperationConst.IsVisible, value);
    }
    get ComponentName(): string {
        return this.get(ViewOperationConst.ComponentName);
    }
    set ComponentName(value: string) {
        this.set(ViewOperationConst.ComponentName, value);
    }
    get IsGenerateComponent(): boolean {
        return this.get(ViewOperationConst.IsGenerateComponent);
    }
    set IsGenerateComponent(value: boolean) {
        this.set(ViewOperationConst.IsGenerateComponent, value);
    }
    get IsRef(): boolean {
        return this.get(ViewOperationConst.IsRef);
    }
    set IsRef(value: boolean) {
        this.set(ViewOperationConst.IsRef, value);
    }
    get ComponentPkgName(): string {
        return this.get(ViewOperationConst.ComponentPkgName);
    }
    set ComponentPkgName(value: string) {
        this.set(ViewOperationConst.ComponentPkgName, value);
    }

    abstract get Parameters(): ViewParameterListBase;

    abstract set Parameters(value: ViewParameterListBase);
}

export class ViewMappedBizAction extends ViewActionBase {

    constructor(action: MappedBizAction | Map<string, any>) {
        super(action);
    }

    protected getParameterList(paras: List<any>): ViewParameterListBase {
        return new ViewBizActionParaList(paras);
    }

    get Parameters(): ViewParameterListBase {
        const paras = this.get(ViewOperationConst.ParameterCollection);
        if (isNullOrUndefined(paras)) {
            this.set(ViewOperationConst.ParameterCollection, new ViewBizActionParaList(null));
        }
        return paras;
    }

    set Parameters(value: ViewParameterListBase) {
        this.set(ViewOperationConst.ParameterCollection, value);
    }
}

export class ViewMappedCdpAction extends ViewActionBase {

    constructor(action: MappedCdpAction | Map<string, any>) {
        super(action);
    }

    protected getParameterList(paras: List<any>): ViewParameterListBase {
        return new ViewCdpActionParaList(paras);
    }

    get Parameters(): ViewParameterListBase {
        const paras = this.get(ViewOperationConst.ParameterCollection);
        if (isNullOrUndefined(paras)) {
            this.set(ViewOperationConst.ParameterCollection, new ViewCdpActionParaList(null));
        }
        return paras;
    }

    set Parameters(value: ViewParameterListBase) {
        this.set(ViewOperationConst.ParameterCollection, value);
    }
}



