import {
    Guid,
    GSPElementObjectType, GSPElementDataType, GSPViewModelElement,
    GSPColumnGenerate, AuthFieldInfo, GSPCommonObjectType, GSPViewObject
} from '../../util/vm-reference';
import { isNullOrUndefined } from 'util';
// import { AuthorizationEntity } from '../refModule/authorization/authorization/entity/authorizationentity';

/**
 * 新建元数据时默认生成的业务对象
 */
export class InitBizObject {
    /**
     * 初始化业务实体对象
     */
    static initBizObject(): GSPViewObject {
        const mainObj = new GSPViewObject();
        const idElement = this.getIDElement();
        mainObj.ID = Guid.newGuid().toString();
        mainObj.Code = 'newObject';
        mainObj.Name = 'newObject';
        mainObj.ColumnGenerateID = new GSPColumnGenerate();
        mainObj.ColumnGenerateID.ElementID = idElement.ID;
        mainObj.ColumnGenerateID.GernerateType = 'Guid';
        mainObj.ContainElements = new Array<GSPViewModelElement>();
        mainObj.ContainElements.push(idElement);
        mainObj.ObjectType = GSPCommonObjectType.ChildObject;

        // this.addElements(mainObj);
        // this.addBizActions(mainObj);
        // this.addAuthField(mainObj);
        return mainObj;
    }

    /**
     * id字段
     */
    private static getIDElement(): GSPViewModelElement {
        const idElement = new GSPViewModelElement();
        idElement.ID = Guid.newGuid().toString();
        idElement.Code = 'ID';
        idElement.Name = 'ID';
        idElement.LabelID = 'ID';
        idElement.MDataType = GSPElementDataType.String;
        idElement.ObjectType = GSPElementObjectType.None;
        idElement.Length = 36;
        idElement.Precision = 0;
        return idElement;
    }

    private static addElements(obj: GSPViewObject) {
        const ele1 = new GSPViewModelElement();
        ele1.ID = Guid.newGuid();
        ele1.Name = '编号';
        ele1.Code = 'Code';
        ele1.LabelID = 'Code';
        const ele2 = new GSPViewModelElement();
        ele2.ID = Guid.newGuid();
        ele2.Name = '姓名';
        ele2.Code = 'Name';
        ele2.LabelID = 'Name';
        const ele3 = new GSPViewModelElement();
        ele3.ID = Guid.newGuid();
        ele3.Name = '部门';
        ele3.LabelID = 'Department';
        ele3.Code = 'Department';
        obj.ContainElements.push(...[ele1, ele2, ele3]);
    }

    private static addAuthField(obj: GSPViewObject) {
        const newAuthField1 = new AuthFieldInfo();
        newAuthField1.AuthFieldID = 'mockId1';
        newAuthField1.AuthFieldName = 'mockName1';
        newAuthField1.AuthorizationID = '';
        const newAuthField2 = new AuthFieldInfo();
        newAuthField2.AuthFieldID = 'mockId2';
        newAuthField2.AuthFieldName = 'mockName2';
        newAuthField2.AuthorizationID = '';
    }
}



