import { Component, OnInit, Input, ViewChild, OnDestroy, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Map } from 'immutable';


import { isNullOrUndefined } from 'util';
import { GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { DropDownListComponent } from '@progress/kendo-angular-dropdowns';
import {
    GSPElementObjectTypes, GSPElementDataTypes,
    GSPViewObject, GSPViewModelElement, GSPElementObjectType,
    ViewObject, ViewElement,
    GSPElementDataType,
    ViewElementConst,
    GSPBizEntityObject,
    GSPBusinessEntity,
    GSPViewModel,
    Guid
} from '../util/vm-reference';
import { EnumEditComponent } from './enum-edit/enum-edit.component';
import { AssociationEditComponent } from './association-edit/association-edit.component';
import { GSPViewModelElementDataTypes } from '../util/ele-collection-util';
import { MetadataTypeName } from '../util/metadata-type';
import { ObjectTypeEditorInVmComponent } from '../refModule/object-type-editor/object-type-editor/object-type-editor-in-vm.component';
import { VmMappingService } from '../service/vm-mapping.service';
import { BizElementsGridComponent } from './biz-elements-grid/biz-elements-grid.component';
import { VmElementUpdateUtil } from '../util/vm-element-update-util';
import { VmElementEditUtil } from '../util/vm-element-edit-util';
import { Observable } from 'rxjs/Observable';

@Component({
    selector: 'app-vm-element-collection',
    templateUrl: './element-collection.component.html',
    styleUrls: ['./element-collection.component.css'],
    providers: [VmMappingService]
})
export class ElementCollectionComponent implements OnInit, OnChanges, OnDestroy {

    @ViewChild('editElements') addElementsFromBe: BizElementsGridComponent;
    @ViewChild('grid') grid: GridComponent;
    @ViewChild('dataType') chooseDataType: DropDownListComponent;
    @ViewChild('enumsComponent') enumsComponent: EnumEditComponent;
    @ViewChild('associationComponent') associationComponent: AssociationEditComponent;
    @ViewChild('objectTypeEditor') objectTypeEditor: ObjectTypeEditorInVmComponent;

    /**
     * 当前对象
     * 字段集合数据源
     */
    @Input() currentViewObject: ViewObject;

    /**
     * 当前业务实体
     */
    @Input() viewModel: GSPViewModel;

    udtType = MetadataTypeName.UnifiedDataType;

    /**
     * 当前对象（只读）
     */
    currentObject: GSPViewObject;
    bizObject: GSPBizEntityObject;
    targetMetadataPkgName: string;
    targetMetadataId: string;
    targetObjId: string;

    get currentViewElement(): ViewElement {
        return this.viewObject.elements.currentSelectItem;
    }

    /**
     * 当前对象的Immutable结构，用于接受属性更改
     * 为何不直接使用@Input的属性？
     * 答：为了在切换对象树焦点时，保留对之前对象的修改能力，先完成对之前修改的赋值，再更新为新的对象，比如，grid.cellClose()方法可找到相应的字段
     */
    viewObject: ViewObject;

    /**
     * 字段列表数据源
     */
    public elements: GSPViewModelElement[];
    public eleSelection: any[] = [];
    public selectableSettings: SelectableSettings = {
        checkboxOnly: false,
        mode: 'single'
    };
    subscriptions = new Array<any>();

    /**
     * 枚举框展示列表
     */
    public objTypes = GSPElementObjectTypes;
    public dataTypes = GSPElementDataTypes;
    public vmVirtualElementDataTypes = GSPViewModelElementDataTypes;

    /**
     * 显示选择业务字段的窗口
     */
    showUdtMetadataWindow = false;

    // constructor(private formBuilder: FormBuilder, private dataService: UdtService) {
    constructor(private formBuilder: FormBuilder, private mappingService: VmMappingService) {
    }

    ngOnInit(): void {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['currentViewObject']) {
            this.closeGridCell();

            // 更新viewObject
            this.viewObject = this.currentViewObject;
            this.refresh();
        }
    }

    ngOnDestroy(): void {
        this.unsubscribeRefreshView();
    }

    closeGridCell() {
        // 关闭所有正在编辑中的单元格
        this.grid.closeCell();
        // 取消所有编辑中的列
        this.grid.cancelCell();
        // 取消新增行
        this.grid.closeRow(-1);
    }

    /**
     * 刷新
     */
    refresh() {
        if (isNullOrUndefined(this.viewObject)) {
            this.elements = null;
            return;
        }

        const vo = this.viewObject.toJS() as GSPViewObject;
        this.currentObject = vo;
        this.targetMetadataPkgName = vo.Mapping.TargetMetadataPkgName;
        this.targetMetadataId = vo.Mapping.TargetMetadataId;
        this.targetObjId = vo.Mapping.TargetObjId;

        this.getRefObject(this.targetMetadataPkgName, this.targetMetadataId, this.targetObjId);
        // this.handleUdtElements(this.elements);
        this.subscribeRefreshView();
    }

    private getRefObject(targetMetadataPkgName: string, targetMetadataId: string, targetObjId: string) {
        // 加载bizObject
        this.mappingService.getRefObjctByID(targetMetadataPkgName, targetMetadataId, targetObjId)
            .subscribe(data => {
                if (data) {
                    this.bizObject = data;
                    if (isNullOrUndefined(this.viewObject.elements)) {
                        this.elements = null;
                    }
                    // 更新当前启用业务字段的be字段
                    this.elements = this.viewObject.elements.toJS();
                    this.loadElementUtilData();
                } else {
                    alert('获取当前对象对应的业务实体对象失败。');
                }
            }, () => alert('获取当前对象对应的业务实体对象失败。'));
    }

    private loadElementUtilData() {
        VmElementEditUtil.currentObject = this.currentObject;

        VmElementUpdateUtil.viewObject = this.viewObject;
        VmElementUpdateUtil.bizObject = this.bizObject;
        VmElementUpdateUtil.currentObject = this.currentObject;
    }

    /**
     * 关联字段展示部分的数据源
     * @param id 字段ID
     */
    getViewElementById(id: string) {
        return VmElementUpdateUtil.getViewElementById(id);
    }
    // /**
    //  * 加载映射的BizObject
    //  */
    // loadBizObject(): Observable<GSPBizEntityObject> {
    //     const vo = this.viewObject.toJS();
    //     const bePkgName = vo.Mapping.TargetMetadataPkgName;
    //     const beId = vo.Mapping.TargetMetadataId;
    //     const objId = vo.Mapping.TargetObjId;
    //     this.mappingService.getRefObjctByID(bePkgName, beId, objId).subscribe(
    //         data => {
    //             if (data) {
    //                 this.bizObject = data;
    //                 VmElementUpdateUtil.bizObject = data;
    //             } else {
    //                 alert('获取当前对象对应的业务实体对象失败。');
    //             }
    //         }, () => alert('获取当前对象对应的业务实体对象失败。'));
    // }

    /**
     * 订阅视图刷新事件
     */
    private subscribeRefreshView() {
        this.unsubscribeRefreshView();

        // 订阅当前关联数据源的值刷新事件
        // 每次刷新后，需重新绑定字段列表的事件
        const refreshViewSubscription = this.viewObject.elements.refreshView.subscribe(
            () => {
                if (isNullOrUndefined(this.viewObject)) {
                    return;
                }
                this.currentObject = this.viewObject.toJS();
                if (isNullOrUndefined(this.viewObject.elements)) {
                    this.elements = null;
                }
                this.elements = this.viewObject.elements.toJS();
            }
        );
        this.subscriptions.push(refreshViewSubscription);
    }

    /**
     * 取消订阅视图刷新事件
     */
    private unsubscribeRefreshView() {
        while (this.subscriptions.length > 0) {
            const subscription = this.subscriptions.pop();
            subscription.unsubscribe();
        }
    }

    /**
     * 单击触发编辑
     * @param param0
     */
    public cellClickHandler(args: any) {
        const { sender, rowIndex, columnIndex, column, dataItem, isEdited } = args;
        const ele = dataItem as GSPViewModelElement;

        // 若为新增行，不响应
        if (rowIndex === -1) {
            return;
        }

        // 选中当前字段
        this.viewObject.elements.setSelectIndexByID(ele.ID);

        // ① 是否可编辑
        const propertyName = column.field;
        if (!VmElementEditUtil.canEditElementPropertyValue(propertyName, ele)) {
            return;
        }

        // ② 编辑
        switch (propertyName) {
            case ViewElementConst.ObjectType:
                this.editObjectType(this.isObjectTypeWindowEditable(ele)); // 编辑对象类型相关内容，此时可编辑
                return;
            default:
                if (!isEdited) {
                    sender.editCell(rowIndex, columnIndex, this.createFormGroup(ele));
                    return;
                }
        }
    }


    /**
     * 改变对象类型
     * 对象类型编辑器发射事件
     */
    changeObjType(objType: GSPElementObjectType) {
        VmElementUpdateUtil.updateRelativeProperty(ViewElementConst.ObjectType, this.currentViewElement.toJS(), objType);
    }

    /**
     * 编辑结束
     * @param args
     */
    public cellCloseHandler(args: any) {
        const { formGroup, dataItem, column } = args;

        if (!formGroup.valid) {
            // 若验证不通过，则编辑器不失焦
            args.preventDefault();
        } else if (formGroup.dirty) {
            const propertyName = column.field;
            const newValue = formGroup.value[propertyName];

            // ① 是否可赋值字段属性
            if (!VmElementUpdateUtil.CanUpdateElementProperty(propertyName, dataItem, newValue)) {
                return;
            }
            // ② 赋值当前属性
            VmElementUpdateUtil.updateCurrentProperty(propertyName, dataItem, newValue);
            // ③ 处理相关的属性[是否多语、关联枚举、标签]
            VmElementUpdateUtil.updateRelativeProperty(propertyName, dataItem, newValue);
        }
    }

    /**
     * 新增字段
     */
    public addHandler() {
        // 加载bizObject
        this.mappingService.getRefObjctByID(this.targetMetadataPkgName, this.targetMetadataId, this.targetObjId)
            .subscribe(data => {
                if (data) {
                    this.bizObject = data;
                    if (isNullOrUndefined(this.viewObject.elements)) {
                        this.elements = null;
                    }
                    // 更新当前启用业务字段的be字段
                    this.elements = this.viewObject.elements.toJS();
                    this.loadElementUtilData();
                    this.addElementsFromBe.load(this.viewObject.toJS().ContainElements, this.bizObject, this.viewObject.toJS().Mapping);
                } else {
                    alert('获取当前对象对应的业务实体对象失败。');
                }
            }, () => alert('获取当前对象对应的业务实体对象失败。'));


        // if (!this.hasSubscribeAddElements) {
        //     // 订阅新增字段
        //     this.mappingService.getRefObjForAddElements.subscribe(bizObj => {
        //         this.addElementsFromBe.load(this.viewObject.toJS().ContainElements, bizObj, this.viewObject.toJS().Mapping);
        //     });
        //     this.hasSubscribeAddElements = true;
        // }

        // if (isNullOrUndefined(this.currentViewObject)) {
        //     alert('请选中要添加字段的对象。');
        //     return;
        // }
        // const vo = this.viewObject.toJS();
        // const bePkgName = vo.Mapping.TargetMetadataPkgName;
        // const beId = vo.Mapping.TargetMetadataId;
        // const objId = vo.Mapping.TargetObjId;
        // this.mappingService.getRefBizObjectForAddElements(bePkgName, beId, objId);
    }

    /**
     * 新增字段弹窗_确定
     */
    getNewViewElements(newViewElements: GSPViewModelElement[]) {
        if (isNullOrUndefined(newViewElements) || newViewElements.length === 0) {
            return;
        }
        newViewElements.forEach(ele => {
            this.addNewElement(ele);
        });
    }

    /**
     * 新增vm虚拟字段
     */
    public addVirtualHandler() {
        const virtualEle = new GSPViewModelElement();
        virtualEle.ID = Guid.newGuid();
        virtualEle.IsVirtual = true;
        virtualEle.IsVirtualViewElement = true;
        // virtualEle.Code = virtualEle.Name = virtualEle.LabelID = this.getNewVirtualElementName();
        virtualEle.Length = 36;
        this.addNewElement(virtualEle);
    }

    // tslint:disable-next-line:member-ordering
    private defaultVirtualEleName = 'vElement';

    getNewVirtualElementName(): string {
        let index = 0;
        if (!this.elements || this.elements.length === 0) {
            return `${this.defaultVirtualEleName}_${index.toString()}`;
        } else {
            let name = `${this.defaultVirtualEleName}_${index.toString()}`;
            while (this.elements.find(ele => ele.LabelID === name)) {
                index++;
                name = `${this.defaultVirtualEleName}_${index.toString()}`;
            }
            return name;
        }
    }

    /**
     * 新增后的行按钮（确认）
     */
    public confirmHandler({ sender, formGroup, rowIndex }) {
        if (formGroup.valid) {
            const ele = this.mergeFormGroupValue(formGroup);
            this.addNewElement(ele);
            sender.closeRow(rowIndex);
        }
    }

    private addNewElement(item: GSPViewModelElement) {
        this.viewObject.elements.add(new ViewElement(Map(item)));
    }

    /**
     * 新增后的行按钮（取消）
     */
    public cancelAddNewItemHandler(args: any) {
        const { sender, rowIndex } = args;
        sender.closeRow(rowIndex);
    }


    /**
     * 删除当前行对应字段（行内置删除按钮）
     * @param param0
     */
    public removeHandler({ sender, dataItem, rowIndex }) {
        if (this.canRemove(dataItem.ID)) {
            this.viewObject.elements.remove(rowIndex);
        }
        sender.cancelCell();
    }

    /**
     * 当前字段是否可删除
     * @param id
     */
    private canRemove(id: string) {
        // ② ID字段不可删除
        if (VmElementEditUtil.isIDElement(id)) {
            alert('ID字段不可删除。');
            return false;
        }
        // ③ ParentID字段不可删除
        if (VmElementEditUtil.isParentIDElement(id)) {
            alert('ParentID字段不可删除。');
            return false;
        }
        // ④ 操作的触发字段不可删除
        if (this.isRequestElement()) {
            return false;
        }
        return true;
    }

    /**
     * 是否操作的触发字段
     */
    isRequestElement(): boolean {
        // const validationCountains = GSPViewObject.countainValidationRequestElement(this.bizEntity, id);
        // const determinationCountains = GSPViewObject.countainDeterminationRequestElement(this.bizEntity, id);
        // if (!isNullOrUndefined(validationCountains)) {
        //     alert(`当前字段为校验规则[${validationCountains}]的触发字段，不可删除。`);
        //     return true;
        // }
        // if (!isNullOrUndefined(determinationCountains)) {
        //     alert(`当前字段为联动规则-数据更新后时机-[${determinationCountains}]的触发字段，不可删除。`);
        //     return true;
        // }
        return false;
    }

    /**
     * 删除选中行的字段（界面删除按钮）
     */
    public removeSelectedElement() {
        if (this.eleSelection.length === 0) { alert('请先选中要删除的字段。'); } else {
            while (this.eleSelection.length !== 0) {
                const id = this.eleSelection.pop();
                if (this.canRemove(id)) {
                    this.viewObject.elements.removeByID(id);
                }
            }
        }
    }

    private createFormGroup(dataItem: any): FormGroup {
        return this.formBuilder.group({
            'Code': [dataItem.Code],
            'Name': [dataItem.Name],
            'LabelID': [dataItem.LabelID],
            // 'Code': [dataItem.Code, Validators.required],
            // 'Name': [dataItem.Name, Validators.required],
            // 'LabelID': [dataItem.LabelID, Validators.required],
            'Length': [dataItem.Length, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,4}')])],
            'Precision': [dataItem.Precision, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,3}')])],
            'DefaultValue': [dataItem.DefaultValue],
            'MDataType': this.getDataTypeWithUdt(dataItem),
            'ObjectType': dataItem.ObjectType,

            'ID': [dataItem.ID], // 不显示，ID
            'Element': [dataItem], // 不显示，保留字段初始化信息，如是否虚拟等信息
        });
    }

    private getDataTypeWithUdt(ele: GSPViewModelElement) {
        if (ele.IsUdt) {
            return ViewElementConst.UnifiedDataType;
        } else {
            return ele.MDataType;
        }
    }

    /**
     * 合并表格中的信息与默认新增信息
     * @param formGroup
     */
    private mergeFormGroupValue(formGroup: FormGroup): GSPViewModelElement {
        if (isNullOrUndefined(formGroup)) {
            alert('当前字段信息未定义。'); // 异常
        }
        const ele = formGroup.value['Element'];
        ele.Code = formGroup.value['Code'];
        ele.Name = formGroup.value['Name'];
        ele.LabelID = formGroup.value['LabelID'];
        ele.Length = formGroup.value['Length'];
        ele.Precision = formGroup.value['Precision'];
        ele.MDataType = formGroup.value['MDataType'];
        ele.ObjectType = formGroup.value['ObjectType'];
        return ele;
    }

    /**
     * 判断是否包含关联列表
     * @param dataItem 当前字段
     */
    public HasAssociation(dataItem: any): boolean {
        const ele = dataItem as GSPViewModelElement;
        if (ele) {
            if (ele.ChildAssociations) {
                return ele.ChildAssociations.length > 0;
            }
            return false;
        } else {
            return false;
        }
    }

    /**
     * 当前字段的对象类型弹窗是否可编辑（是否只读显示）
     * ①引用udt的字段不可编辑对象类型
     * ②be带出字段不可编辑对象类型
     * ③vm字段可编辑
     */
    private isObjectTypeWindowEditable(ele: GSPViewModelElement): boolean {
        // 引用udt的字段不可编辑对象类型
        if (ele.IsUdt) {
            return false;
        }
        // be带出字段不可编辑对象类型
        if (!ele.IsVirtualViewElement) {
            return false;
        }
        // vm字段可编辑
        return true;
    }

    /**
     * 显示对象类型弹窗编辑对象类型
     * @param dataItem
     */
    private editObjectType(canEdit: boolean) {
        this.objectTypeEditor.load(this.currentViewElement, canEdit);
    }

    /**
     * 修改[是否多语]复选框状态
     * @param newValue 是否多语
     * @param dataItem 当前字段
     */
    changeIsMultiLanguageCheck(newValue: boolean, dataItem: GSPViewModelElement) {
        VmElementUpdateUtil.changeIsMultiLanguageCheck(newValue, dataItem);
    }
}




