import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { MapBase } from '../base/mapBase';
import { ViewEnumValueList } from '../viewEnumValue/viewEnumValueList';
import { ViewAssociationList } from '../viewAssociation/viewAssociationList';

import { GSPViewModelElement, ViewElementConst } from '../../ref';
import { ViewMapping } from '../viewMapping/viewMapping';

export class ViewElement extends MapBase {

    /**
     * 构造函数
     * @param element
     */
    constructor(element: GSPViewModelElement | Map<string, any>) {
        super(element);
        if (isNullOrUndefined(element)) {
            return;
        }
        this.data = this.getViewElement(this.data);
    }

    /**
     * 获取字段的Map
     * @param element
     */
    private getViewElement(element: Map<string, any>): any {
        const viewElement = element.update(
            ViewElementConst.ContainEnumValues, enums => this.getEnumValues(enums)
        ).update(ViewElementConst.ChildAssociations, assos => this.getViewAssos(assos))
            .update(ViewElementConst.Mapping, mapping => this.getViewMapping(mapping));

        return viewElement;
    }

    private getViewMapping(mapping: Map<string, any>) {
        if (isNullOrUndefined(mapping)) {
            return;
        }
        const viewValueHelpConfigs = new ViewMapping(mapping);
        return viewValueHelpConfigs;
    }

    private getEnumValues(enums: List<any>): ViewEnumValueList {
        return new ViewEnumValueList(enums);
    }

    private getViewAssos(assos: List<any>): ViewAssociationList {
        const assoList = new ViewAssociationList(assos);
        assoList.refreshView.subscribe(item => {
            this.refreshViewLayer();
        });
        return assoList;
    }

    set enumValues(enums: ViewEnumValueList) {
        this.set(ViewElementConst.ContainEnumValues, enums);
    }

    get enumValues(): ViewEnumValueList {
        return this.get(ViewElementConst.ContainEnumValues);
    }

    get associations(): ViewAssociationList {
        return this.get(ViewElementConst.ChildAssociations);
    }

    set associations(assos: ViewAssociationList) {
        this.set(ViewElementConst.ChildAssociations, assos);
    }

    get objectType(): string {
        return this.get(ViewElementConst.ObjectType);
    }

    set objectType(type: string) {
        this.set(ViewElementConst.ObjectType, type);
    }

    clearEnumValues() {
        this.data = this.data.update(ViewElementConst.ContainEnumValues, list => {
            return new ViewEnumValueList(null);
        });
    }

    clearAssociations() {
        this.data = this.data.update(ViewElementConst.ChildAssociations, list => {
            return new ViewAssociationList(null);
        });
    }
}



