import { Component, OnInit, Input, OnChanges, OnDestroy, SimpleChanges } from '@angular/core';
import {
  ViewModel, ViewObject, GSPViewModel, BizActionParameter, Guid
} from '../../util/vm-reference';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-biz-entity-info-card',
  templateUrl: './biz-entity-info-card.component.html',
  styleUrls: ['./biz-entity-info-card.component.css']
})
export class BizEntityInfoCardComponent implements OnInit, OnChanges, OnDestroy {

  @Input() currentViewModel: ViewModel;

  showAuthorizationWindow = false;

  public formGroup: FormGroup;

  bizEntity: GSPViewModel;

  /**
   * 临时代替数据授权对象
   */
  paras: BizActionParameter[];
  /**
   * viewAction视图层刷新事件订阅
   */
  private refreshSubscriptions = new Array<Subscription>();

  /**
   * 表单值更新事件订阅
   * 更改类型为Subscription时，报错：类型冲突
   */
  private formGroupSubscriptions: any[];

  constructor(private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.refresh();
  }

  ngOnDestroy(): void {
    this.unsubscribeRefreshLayer();
  }

  /**
   * 加载
   */
  private load(): any {
    this.createForm();
    this.refresh();
  }

  /**
   * 刷新
   */
  private refresh() {
    if (isNullOrUndefined(this.currentViewModel)) {
      this.clearFormValue();
      return;
    }

    this.unsubscribeRefreshLayer();
    this.bizEntity = this.currentViewModel.toJS();
    this.setFormValue(this.bizEntity);
    this.subscribeRefreshViewLayer();
  }

  /**
   * 初始化表单
   */
  private createForm() {
    this.formGroup = this.formBuilder.group({
      code: ['', Validators.required],
      name: ['', Validators.required],
      generatingAssembly: [''],
      componentAssemblyName: [''],
      isUsingTimeStamp: false
    });
  }

  /**
   * 表单赋值
   * @param be
   */
  private setFormValue(be: GSPViewModel) {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(be)) {
      this.clearFormValue();
    }
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.enable();
    this.formGroup.patchValue({
      code: be.Code || '',
      name: be.Name || '',
      generatingAssembly: be.GeneratingAssembly || '',
      // componentAssemblyName: be.ComponentAssemblyName || '',
      // isUsingTimeStamp: be.IsUsingTimeStamp
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 表单清空
   */
  private clearFormValue() {
    this.unsubscribeFormGroupValueChanges();
    if (isNullOrUndefined(this.formGroup)) {
      this.createForm();
    }
    this.formGroup.disable();
    this.formGroup.patchValue({
      code: '',
      name: '',
      generatingAssembly: '',
      componentAssemblyName: '',
      isUsingTimeStamp: false
    });
    this.subscribeFormGroupValueChanges();
  }

  /**
   * 订阅表单值改变事件
   */
  private subscribeFormGroupValueChanges() {
    const subscriptionCode = this.formGroup.controls.code.valueChanges.subscribe(code => {
      this.currentViewModel.Code = code;
    });
    const subscriptionName = this.formGroup.controls.name.valueChanges.subscribe(name => this.currentViewModel.Name = name);
    // const subscriptionBeAssemblyName = this.formGroup.controls.generatingAssembly.valueChanges.subscribe(assemblyName =>
    //   this.currentViewModel.GeneratingAssembly = assemblyName);
    // const subscriptionCmpAssemblyName = this.formGroup.controls.componentAssemblyName.valueChanges.subscribe(assemblyName =>
    //   this.currentViewModel.ComponentAssemblyName = assemblyName);
    // const subscriptionIsUsingTimeStamp = this.formGroup.controls.isUsingTimeStamp.valueChanges.subscribe(isUsingTimeStamp =>
    //   this.currentViewModel.IsUsingTimeStamp = isUsingTimeStamp);
    if (isNullOrUndefined(this.formGroupSubscriptions)) {
      this.formGroupSubscriptions = new Array<any>();
    }
    this.formGroupSubscriptions.push(subscriptionCode);
    this.formGroupSubscriptions.push(subscriptionName);
    // this.formGroupSubscriptions.push(subscriptionBeAssemblyName);
    // this.formGroupSubscriptions.push(subscriptionCmpAssemblyName);
    // this.formGroupSubscriptions.push(subscriptionIsUsingTimeStamp);
  }

  /**
   * 订阅viewAction视图层刷新事件
   */
  subscribeRefreshViewLayer(): any {
    const subscription = this.currentViewModel.refreshView.subscribe(value => {
      this.bizEntity = value.toJS();
      this.setFormValue(this.bizEntity);
    });
    this.refreshSubscriptions.push(subscription);
  }

  /**
   * 取消订阅viewAction视图层刷新事件
   */
  private unsubscribeRefreshLayer() {
    if (this.refreshSubscriptions) {
      while (this.refreshSubscriptions.length > 0) {
        const subscription = this.refreshSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }

  /**
   * 取消订阅表单值改变事件
   */
  private unsubscribeFormGroupValueChanges() {
    if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
      while (this.formGroupSubscriptions.length > 0) {
        const subscription = this.formGroupSubscriptions.pop();
        subscription.unsubscribe();
      }
    }
  }

}



