import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import {
  IGSPCommonElement, IGSPCommonObject, ViewElementConst,
  GSPViewModelElement, GSPBizEntityElement, ViewModelMapping
} from '../../util/vm-reference';
import { isNullOrUndefined } from 'util';
import { VmMappingService } from '../../service/vm-mapping.service';

@Component({
  selector: 'app-biz-elements-grid',
  templateUrl: './biz-elements-grid.component.html',
  styleUrls: ['./biz-elements-grid.component.css'],
  providers: [VmMappingService]
})
export class BizElementsGridComponent implements OnInit, OnChanges {

  /**
   * 触发字段数据源
   */
  @Input() currentElements: GSPViewModelElement[];
  bizObject: IGSPCommonObject;
  @Input() mapping: ViewModelMapping;


  elements: IGSPCommonElement[];
  bizObjects: any[];
  active = false;
  identifier = ViewElementConst.IDENTIFIER;

  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<GSPViewModelElement[]> = new EventEmitter();
  /**
   * 允许多选
   */
  private selectableSettings: SelectableSettings = {
    mode: 'multiple',
    checkboxOnly: true
  };

  /**
   * 当前引用字段id集合，设置为选中
   */
  private eleSelection: string[] = [];

  constructor(private vmMappingService: VmMappingService) { }

  ngOnInit() {
    this.vmMappingService.addNewVmElements.subscribe(newVmElements => {
      this.save.next(newVmElements as GSPViewModelElement[]);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {

  }

  /**
   * 加载
   * @param refViewElements 关联字段集合
   * @param refModel 关联模型
   */
  load(triggerElements: GSPViewModelElement[], bizObject: IGSPCommonObject, objMapping: ViewModelMapping) {
    this.active = true;
    this.currentElements = triggerElements;
    this.bizObject = bizObject;
    this.mapping = objMapping;
    this.getObjectElements(bizObject);

    // 若无可新增字段，弹出提示，关闭弹窗
    if (this.elements.length === 0) {
      alert('当前对象的字段已全部添加。');
      this.closeForm();
    }
  }

  /**
   * 获取当前引用字段集合
   * @param refModel 关联模型
   */
  private getObjectElements(currentObject: IGSPCommonObject): any {
    const bizElements = currentObject.ContainElements;
    if (isNullOrUndefined(this.currentElements) || this.currentElements.length === 0) {
      this.elements = bizElements;
      return;
    } else {
      this.elements = new Array<GSPBizEntityElement>();
      bizElements.forEach(ele => {
        if (!this.currentElements.find(ve => (!(<GSPViewModelElement>ve).IsVirtualViewElement && ele.ID === ve.Mapping.TargetObjId))) {
          const bizEle = ele as GSPBizEntityElement;
          this.elements.push(bizEle);
        }
      });
    }
  }

  public onConfirm(e): void {
    e.preventDefault();
    // 无新增项
    if (this.eleSelection.length === 0) {
      this.closeForm();
    } else {
      // 有新增项
      const newBizElements = new Array<GSPBizEntityElement>();
      this.eleSelection.forEach(eleId => {
        const bizEle = this.elements.find(ele => ele.ID === eleId) as GSPBizEntityElement;
        newBizElements.push(bizEle);
      });
      this.vmMappingService.convertBizElementsToViewElements(newBizElements, this.mapping);
      this.closeForm();
    }
  }

  public onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.eleSelection = [];
    this.active = false;
  }
}



