import { List, Map } from 'immutable';

import { ViewMappedBizAction, ViewMappedCdpAction, ViewActionBase } from './viewActionBase';

import { ViewModelAction, ViewOperationConst, ViewModelActionType, MappedCdpAction } from '../../ref';
import { ListBase } from '../base/listBase';
import { isNullOrUndefined } from 'util';

export class ViewActionList extends ListBase<ViewActionBase> {

    constructor(bizActionArray: ViewModelAction[] | List<any>) {
        super(bizActionArray);
        if (bizActionArray) {
            this.data = this.getViewOperations(this.data);
        }
    }

    private getViewOperations(bizActionArray: List<any>): List<ViewActionBase> {
        if (isNullOrUndefined(bizActionArray)) {
            return;
        }

        let viewMgrActionList = bizActionArray;

        if (bizActionArray.size > 0) {
            for (let i = 0; i < bizActionArray.size; i++) {
                viewMgrActionList = viewMgrActionList.update(i, () => this.getViewOperation(bizActionArray.get(i)));
            }
            return viewMgrActionList;
        }
    }

    protected getViewOperation(action: ViewModelAction): ViewActionBase {
        const type = action.Type;
        switch (type) {
            case ViewModelActionType.VMAction:
                return new ViewMappedCdpAction(action as MappedCdpAction);
            case ViewModelActionType.BEAction:
            case ViewModelActionType.Custom:
            default:
                return new ViewMappedBizAction(action);
        }
    }
}



