import { List } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { ListBase } from '../base/listBase';
import { ViewAssociation } from './viewAssociation';

import { GSPAssociation } from '../../ref';

export class ViewAssociationList extends ListBase<ViewAssociation> {

    public confirmAssociations: Subject<this> = new Subject<this>();

    constructor(assoArray: any) {
        super(assoArray);
        if (isNullOrUndefined(assoArray)) {
            return;
        } else {
            this.data = this.getViewAssociations(this.data);
        }
        // if (assoArray) {
        //     this.data = this.getViewAssociations(this.data);
        // }
        // else {
        //     this.data = List();
        // }
    }

    private getViewAssociations(assoArray: List<any>): any {
        if (isNullOrUndefined(assoArray)) {
            return;
        }

        let assoList = assoArray;

        if (assoArray.size > 0) {
            for (let i = 0; i < assoArray.size; i++) {
                assoList = assoList.update(i, value => this.getViewAssociation(assoArray.get(i)));
            }
            return assoList;
        }
    }

    private getViewAssociation(asso: GSPAssociation): any {
        return new ViewAssociation(asso);
    }

    // /**
    //  * 当前关联键值对
    //  */
    // get viewAssoKeys(): viewAssoKeyList {
    //     if (isNullOrUndefined(this.currentSelectItem)) {
    //         return;
    //     }
    //     let assoKeys = this.currentSelectItem.get(bizElementConst.KeyCollection) as viewAssoKeyList;
    //     return assoKeys;
    // }

    // /**
    //  * 当前关联键值对
    //  */
    // get viewRefElements(): refViewElementList {
    //     if (isNullOrUndefined(this.currentSelectItem)) {
    //         return;
    //     }
    //     let refElements = this.currentSelectItem.get(bizElementConst.RefElementCollection) as refViewElementList;
    //     return refElements;
    // }
}






