import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { of } from 'rxjs/observable/of';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { AuthorizationService } from '../../service/authorization.service';
import { AuthoperationEntity } from '../../entity/authoperationentity';

@Component({
  selector: 'app-authoperation-tree',
  templateUrl: './authoperation-tree.component.html',
  styleUrls: ['./authoperation-tree.component.css']
})
export class AuthoperationTreeComponent implements OnInit {

  datas: AuthoperationEntity[] = [];
  selectedItem: AuthoperationEntity;
  treeElement: AuthoperationEntity;

  @Output() authoperationEntity: AuthoperationEntity;


  // @Input
  // @Input() selected: string;
  @Input() authorizationId: string;
  @Input() authfield: string;

  @Output() selectedChange = new EventEmitter<AuthoperationEntity>();

  constructor(private authorizationService: AuthorizationService) { }

  ngOnInit() {
    this.treeElement = new AuthoperationEntity();
    this.authorizationService.GetAuthOperations(this.authorizationId, this.authfield).subscribe(data => {
      data.forEach(element => {
        this.datas.push(element);
      });
    });
  }

  dialogOk() {
    return this.selectedItem;
  }

    // 弹出框 分配的功能操作控件图标样式
    iconClass({ type }: any): any {
      return {
        'k-i-gear': true,
        'k-icon': true
      };
    }

  // 权限字段切换事件
  handleSelection({ dataItem }: any): void {
    this.selectedItem = dataItem;
  }

  public hasChildren = (item: any) => item.children && item.children.length > 0;
  public fetchChildren = (item: any) => of(item.children);
}



