import {
    BizParameterTypes, ViewModelReturnValue, ViewActionBase, VMParameterType,
    ViewModelVoidReturnValue, Guid, ViewModelParameter, ViewReturnValue, VMParameterTypes
} from '../../../util/vm-reference';
import { Component, OnInit, Input, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { Subscription } from 'rxjs/Subscription';
/**
 * action卡片的基类
 */
@Component({
    selector: 'app-return-info-card',
    templateUrl: './return-info-card.component.html',
    styleUrls: ['./return-info-card.component.css']
})
export class ReturnInfoCardComponent implements OnInit, OnChanges, OnDestroy {

    /**
     * 当前动作
     */
    @Input() currentViewAction: ViewActionBase;
    @Input() isEditable: boolean;

    private paramTypes = VMParameterTypes;

    returnValue: ViewModelReturnValue;

    public formGroup: FormGroup;

    /**
     * MgrAction-显示'是否可见';BeAction不显示
     */
    public isMgrAction = true;

    /**
     * viewAction视图层刷新事件订阅
     */
    private refreshSubscriptions = new Array<Subscription>();

    /**
     * 表单值更新事件订阅
     * 更改类型为Subscription时，报错：类型冲突
     */
    private formGroupSubscriptions: any[];

    ngOnInit() {
        this.load();
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.refresh();
    }

    ngOnDestroy(): void {
        this.unsubscribeRefreshLayer();
        this.unsubscribeFormGroupValueChanges();
    }

    constructor(private formBuilder: FormBuilder) {

    }

    /**
     * 加载
     */
    private load(): any {
        this.createForm();
        this.refresh();
    }

    /**
     * 刷新
     */
    refresh() {
        if (isNullOrUndefined(this.currentViewAction)) {
            this.clearAndDisable();
            return;
        }
        if (!isNullOrUndefined(this.currentViewAction) && isNullOrUndefined(this.currentViewAction.ReturnValue)) {
            const returnValue = this.createNewBizVoidReturnType();
            this.currentViewAction.ReturnValue = new ViewReturnValue(returnValue);
        }
        this.unsubscribeRefreshLayer();
        this.unsubscribeFormGroupValueChanges();
        this.enableFormGroup();
        this.returnValue = this.currentViewAction.ReturnValue.toJS();
        this.setFormValue(this.returnValue);
        this.subscribeRefreshViewLayer();
        this.subscribeFormGroupValueChanges();
    }

    private createNewBizVoidReturnType(): ViewModelVoidReturnValue {
        const returnValue = new ViewModelVoidReturnValue();
        returnValue.ID = Guid.newGuid();
        return returnValue;
    }

    private createNewBizReturnType(): ViewModelReturnValue {
        const returnValue = new ViewModelReturnValue();
        returnValue.ID = Guid.newGuid();
        const stringType = VMParameterType.String;
        returnValue.ParameterType = stringType;
        returnValue.ClassName = this.getClassName(stringType);
        returnValue.Assembly = this.getAssembly(stringType);
        return returnValue;
    }

    /**
     * 初始化表单
     */
    private createForm() {
        this.formGroup = this.formBuilder.group({
            IsUseReturnType: false,
            Assembly: '',
            ClassName: '',
            ParamDescription: '',
            ParameterType: ''
        });
    }

    /**
     * 表单赋值
     * @param returnValue
     */
    private setFormValue(returnValue: ViewModelReturnValue) {
        this.unsubscribeFormGroupValueChanges();
        if (isNullOrUndefined(returnValue)) {
            this.clearFormValue();
            return;
        }
        // 若为无返回值类型，也需要清空输入值
        if (!this.getIsUseReturnType(returnValue)) {
            this.clearFormValue();
            return;
        }
        if (isNullOrUndefined(this.formGroup)) {
            this.createForm();
        }
        this.formGroup.enable();
        if (returnValue.ParameterType !== VMParameterType.Custom) {
            this.formGroup.patchValue({
                IsUseReturnType: this.getIsUseReturnType(returnValue),
                Assembly: this.GetParamAssemblyDisplay(returnValue),
                ClassName: this.GetParamClassNameDisplay(returnValue),
                ParamDescription: returnValue.ParamDescription || '',
                ParameterType: isNullOrUndefined(returnValue.ParameterType) ? '' : returnValue.ParameterType
                // Tips:
                // 此处有关参数类型赋值，因为枚举值可能为0,0的时候进行或操作，会被判断为'否'，使用''，导致清空string选项
                // 正确的写法应该如上
            });
        } else {
            this.formGroup.patchValue({
                IsUseReturnType: this.getIsUseReturnType(returnValue),
                Assembly: returnValue.Assembly || '',
                ClassName: returnValue.ClassName || '',
                ParamDescription: returnValue.ParamDescription || '',
                ParameterType: isNullOrUndefined(returnValue.ParameterType) ? '' : returnValue.ParameterType
            });
        }
        this.subscribeFormGroupValueChanges();
        this.setDisabledFormGroup();
    }

    /**
     * 返回值表单不可编辑
     * ①选择已有构件；
     * ②dbe带出的操作；
     */
    setDisabledFormGroup() {
        if (!this.isEditable) {
            this.disableFormGroup();
        }
    }

    /**
     * 判断是否为空返回值
     * @param returnValue
     */
    getIsUseReturnType(returnValue: ViewModelReturnValue): boolean {
        if (isNullOrUndefined(returnValue)) {
            return false;
        }
        if (returnValue.ClassName === 'Void' && returnValue.Assembly === 'mscorlib.dll') {
            return false;
        } else {
            return true;
        }
    }

    /**
     * 表单清空
     */
    private clearFormValue() {
        this.unsubscribeFormGroupValueChanges();
        if (isNullOrUndefined(this.formGroup)) {
            this.createForm();
        }

        this.formGroup.patchValue({
            IsUseReturnType: false,
            ParameterType: '',
            Assembly: '',
            ClassName: '',
            ParamDescription: '',
        });
        // 相关控件不可编辑
        this.disableReturnInfoControls();
        this.subscribeFormGroupValueChanges();
        this.setDisabledFormGroup();
    }

    /**
     * 若为[无返回值]，则控件不可编辑
     */
    private disableReturnInfoControls() {
        this.formGroup.controls.ParameterType.disable();
        this.formGroup.controls.Assembly.disable();
        this.formGroup.controls.ClassName.disable();
        this.formGroup.controls.ParamDescription.disable();
    }

    /**
     * 表单不可编辑
     */
    private disableFormGroup() {
        this.unsubscribeFormGroupValueChanges();
        if (isNullOrUndefined(this.formGroup)) {
            this.createForm();
        }
        this.formGroup.disable();
        this.subscribeFormGroupValueChanges();
    }

    /**
     * 清空表单，并不可编辑
     */
    private clearAndDisable() {
        this.clearFormValue();
        this.disableFormGroup();
    }

    /**
     * 使表单可编辑
     */
    private enableFormGroup() {
        this.unsubscribeFormGroupValueChanges();
        if (isNullOrUndefined(this.formGroup)) {
            this.createForm();
        }
        this.formGroup.enable();
        this.subscribeFormGroupValueChanges();
    }

    /**
     * 参数程序集名
     * @param dataItem
     */
    GetParamAssemblyDisplay(dataItem: ViewModelParameter): string {
        if (dataItem.ParameterType === VMParameterType.Custom) {
            return dataItem.Assembly;
        } else {
            return '';
        }
    }

    /**
     * 参数类名
     * @param dataItem
     */
    GetParamClassNameDisplay(dataItem: ViewModelParameter): string {
        if (dataItem.ParameterType === VMParameterType.Custom) {
            return dataItem.ClassName;
        } else {
            return '';
        }
    }

    /**
     * 订阅表单值改变事件
     */
    private subscribeFormGroupValueChanges() {
        // ① 返回值是否为void
        const subscriptionIsUseReturnType = this.formGroup.controls.IsUseReturnType.valueChanges.subscribe(
            hasReturnType => {
                if (!hasReturnType) {
                    this.currentViewAction.ReturnValue = new ViewReturnValue(this.createNewBizVoidReturnType());
                } else {
                    this.currentViewAction.ReturnValue = new ViewReturnValue(this.createNewBizReturnType());
                }
                this.refresh();
            });
        // ② assembly
        const subscriptionAssembly = this.formGroup.controls.Assembly.valueChanges.subscribe(
            assembly => {
                this.currentViewAction.ReturnValue.Assembly = assembly;
                this.refresh();
            });
        // ③ className
        const subscriptionClassName = this.formGroup.controls.ClassName.valueChanges.subscribe(
            code => {
                this.currentViewAction.ReturnValue.ClassName = code;
                this.refresh();
            });
        // ⑤ description
        const subscriptionDes = this.formGroup.controls.ParamDescription.valueChanges.subscribe(
            description => {
                this.currentViewAction.ReturnValue.ParamDescription = description;
                this.refresh();
            }
        );
        if (isNullOrUndefined(this.formGroupSubscriptions)) {
            this.formGroupSubscriptions = new Array<any>();
        }
        this.formGroupSubscriptions.push(subscriptionIsUseReturnType);
        this.formGroupSubscriptions.push(subscriptionAssembly);
        this.formGroupSubscriptions.push(subscriptionClassName);
        this.formGroupSubscriptions.push(subscriptionDes);
    }

    /**
     * 订阅viewAction视图层刷新事件
     */
    subscribeRefreshViewLayer(): any {
        const subscription = this.currentViewAction.ReturnValue.refreshView.subscribe(() => {
            this.refresh();
        });
        this.refreshSubscriptions.push(subscription);
    }

    /**
     * 取消订阅viewAction视图层刷新事件
     */
    private unsubscribeRefreshLayer() {
        if (this.refreshSubscriptions) {
            while (this.refreshSubscriptions.length > 0) {
                const subscription = this.refreshSubscriptions.pop();
                subscription.unsubscribe();
            }
        }
    }

    /**
     * 取消订阅表单值改变事件
     */
    private unsubscribeFormGroupValueChanges() {
        if (this.formGroupSubscriptions && this.formGroupSubscriptions.length > 0) {
            while (this.formGroupSubscriptions.length > 0) {
                const subscription = this.formGroupSubscriptions.pop();
                subscription.unsubscribe();
            }
        }
    }

    getAssembly(type: VMParameterType): string {
        const paraType = this.paramTypes.find(item => item.value === type);
        return paraType ? paraType.assembly : '';
    }

    getClassName(type: VMParameterType): string {
        const paraType = this.paramTypes.find(item => item.value === type);
        return paraType ? paraType.className : '';
    }

    /**
     * 返回值类型切换事件
     * @param selectedIndex 返回值类型集合中的索引
     */
    selectType({ value }: any) {
        // ④ 更改类型
        this.currentViewAction.ReturnValue.ParameterType = value;
        this.currentViewAction.ReturnValue.Assembly = this.getAssembly(value);
        this.currentViewAction.ReturnValue.ClassName = this.getClassName(value);
    }


    /**
     * 判断是否为自定义类型
     * 是否将程序集名/类名输入框置为只读状态
     */
    get isCustomType(): boolean {
        if (isNullOrUndefined(this.returnValue)) {
            return false;
        }
        return this.returnValue.ParameterType === VMParameterType.Custom;
    }

    /**
     * 外部控制不可编辑
     */
    disabled() {
        this.disableFormGroup();
    }
}






