import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AuthorizationSelectComponent } from './authorizationobject/authorization-select/authorization-select.component';
import { AuthorizationTreeComponent } from './authorizationobject/authorization-tree/authorization-tree.component';
import { FormsModule } from '@angular/forms';
import { ModalModule } from '@farris/ui/container/modal/modal';
// import { ModalModule } from '@farris/ui/container/modal/modal';

import { LayoutModule } from '@progress/kendo-angular-layout';
// import { BrowserModule } from '@angular/platform-browser';
// import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TreeViewModule } from '@progress/kendo-angular-treeview';

import { AuthorizationService } from './service/authorization.service';



import { AuthoperationSelectComponent } from './authoperation/authoperation-select/authoperation-select.component';
import { AuthoperationTreeComponent } from './authoperation/authoperation-tree/authoperation-tree.component';


import { AuthoperationComponent } from './authoperation/authoperation.component';


@NgModule({
  imports: [
    LayoutModule,
    CommonModule,
    ModalModule.forRoot(),
    FormsModule,
    TreeViewModule
  ],
  entryComponents: [AuthorizationSelectComponent, AuthoperationSelectComponent],
  declarations: [
    AuthorizationSelectComponent,
    AuthorizationTreeComponent,
    AuthoperationSelectComponent,
    AuthoperationTreeComponent,
    AuthoperationComponent
  ],
  exports: [AuthorizationSelectComponent, AuthoperationSelectComponent],
  providers: [AuthorizationService]
})

export class AuthorizationModule { }




