import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { VmDesignerFrameModule } from './vmdesigner-frame/vm-designer-frame.module';
import { VmDesignerFrameComponent } from './vmdesigner-frame/vm-designer-frame.component';
export class VmDesignerPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(VmDesignerFrameComponent);
    const moduleRef = this.injector.get(VmDesignerFrameModule) as VmDesignerFrameModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}

