import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import {
  GSPBusinessEntity, ViewModelAction, ViewModelMapping, ViewOperationConst,
  BizMgrAction, ViewModelActionType, Metadata4RefDto, MappedBizAction, HttpUrlConst
} from '../../util/vm-reference';
import { isNullOrUndefined } from 'util';
import { InternalMgrActionUtil } from '../../util/internal-mgr-action-util';
import { VmMetadataService } from '../../service/vm-metadata.service';
import { MetadataDto } from '@gsp-lcm/metadata-selector/metadata/metadata4refdto';
import { VmMappingService } from '../../service/vm-mapping.service';

@Component({
  selector: 'be-mgr-actions-grid',
  templateUrl: './be-mgr-actions-grid.component.html',
  styleUrls: ['./be-mgr-actions-grid.component.css'],
  providers: [VmMetadataService, VmMappingService]
})
export class BeMgrActionsGridComponent implements OnInit, OnChanges {

  mapping: ViewModelMapping;
  /**
   * 触发字段数据源
   */
  currentActions: ViewModelAction[];
  currentBe: GSPBusinessEntity;

  actions: BizMgrAction[];
  active = false;
  identifier = ViewOperationConst.IDENTIFIER;

  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<MappedBizAction[]> = new EventEmitter();
  /**
   * 允许多选
   */
  private selectableSettings: SelectableSettings = {
    mode: 'multiple',
    checkboxOnly: true
  };

  /**
   * 当前引用字段id集合，设置为选中
   */
  private actionSelection: string[] = [];

  constructor(private vmMappingService: VmMappingService, private metadataService: VmMetadataService) { }

  ngOnInit() {
    this.vmMappingService.addNewVmActions.subscribe(newVmActions => {
      this.save.next(newVmActions as MappedBizAction[]);
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  /**
   * 加载
   * @param refViewElements 关联字段集合
   * @param refModel 关联模型
   */
  load(currentActions: ViewModelAction[], mapping: ViewModelMapping) {
    this.mapping = mapping;
    this.metadataService.getMappingBeMetadata(mapping.TargetMetadataPkgName, mapping.TargetMetadataId).subscribe(beMetadata => {
      const beJson = (<MetadataDto>beMetadata).content;
      this.active = true;
      this.currentActions = currentActions;
      this.currentBe = JSON.parse(beJson);
      this.getMgrActions(this.currentBe);

      // 若无可新增字段，弹出提示，关闭弹窗
      if (this.actions.length === 0) {
        alert('业务实体映射操作已全部添加。');
        this.closeForm();
      }
    });
  }

  /**
   * 获取当前引用字段集合
   * @param be 业务实体
   */
  private getMgrActions(be: GSPBusinessEntity): any {
    const actions = be.BizMgrActions;
    if (isNullOrUndefined(this.currentActions) || this.currentActions.length === 0) {
      this.actions = new Array<BizMgrAction>();
      actions.forEach(mgrAction => {
        // ①不包含InternalMgrAction
        if (!InternalMgrActionUtil.isInternalMgrAction(mgrAction)) {
          const bizEle = mgrAction as BizMgrAction;
          this.actions.push(bizEle);
        }
      });
      return;
    } else {
      this.actions = new Array<BizMgrAction>();
      actions.forEach(mgrAction => {
        // ①不包含InternalMgrAction
        // ②不包含已添加的MgrAction
        if (!InternalMgrActionUtil.isInternalMgrAction(mgrAction)) {
          if (!this.currentActions.find(ve => (ve.Type === ViewModelActionType.BEAction && mgrAction.ID === ve.Mapping.TargetObjId))) {
            const bizEle = mgrAction as BizMgrAction;
            this.actions.push(bizEle);
          }
        }
      });
    }
  }

  public onConfirm(e): void {
    e.preventDefault();
    // 无新增项
    if (this.actionSelection.length === 0) {
      this.closeForm();
    } else {
      // 有新增项
      const newMgrActions = new Array<BizMgrAction>();
      this.actionSelection.forEach(actionId => {
        const bizEle = this.actions.find(action => action.ID === actionId) as BizMgrAction;
        newMgrActions.push(bizEle);
      });
      this.vmMappingService.convertBizMgrActionsToViewActions(newMgrActions, this.mapping);
      this.closeForm();
    }
  }

  public onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.actionSelection = [];
    this.active = false;
  }
}



