import {
  GSPViewModelElement, GSPBizEntityObject, GSPElementObjectTypes, GSPElementDataTypes,
  ViewElementConst, GSPElementDataType, GSPElementObjectType, GSPViewObject
} from './libs';
import { isNullOrUndefined } from 'util';


export class VmElementEditUtil {
  static currentObject: GSPViewObject;

  private static dataTypes = GSPElementDataTypes;


  /**
   * 字段当前属性是否可编辑
   * @param dataItem 字段
   */
  static canEditElementPropertyValue(propertyName: string, ele: GSPViewModelElement): boolean {
    // ① ID字段不可编辑
    if (this.isIDElement(ele.ID)) {
      return false;
    }

    // ② 20180915修改_自增对象的ParentID的[编号][名称][标签]可修改
    // be带出对象的ParentID的[编号][名称]可修改，[标签]不可修改
    if (this.isParentIDElement(ele.ID)) {
      switch (propertyName) {
        case ViewElementConst.Code:
        case ViewElementConst.Name:
          return true;
        case ViewElementConst.LabelID:
          return this.isVmVirtualEle(ele); // vm自增字段可改，biz带出字段不可改
        default:
          return false;
      }
    }

    // ③ be带出字段可编辑仅可编辑[编号][名称]
    // 可有条件的编辑[长度][精度](可改小不可改大)
    // 可展示对象类型编辑器，[关联][枚举]
    if (!this.isVmVirtualEle(ele)) {
      switch (propertyName) {
        case ViewElementConst.Code:
        case ViewElementConst.Name:
        case ViewElementConst.DefaultValue:
        case ViewElementConst.Length:
        case ViewElementConst.Precision:
          break;
        case ViewElementConst.ObjectType:
          // 可看枚举，不需要看关联
          if (ele.ObjectType === GSPElementObjectType.Enum) {
            break;
          } else {
            return false;
          }
        default:
          return false;
      }
    }

    // ④ 共同处理
    // ④-1 若启用业务字段,则根据约束/模板来判断属性是否可更改
    // 不可更改，直接返回；
    // 若可更改，则继续；
    if ((<GSPViewModelElement>ele).IsUdt) {
      if (!this.editElementWithUdt(propertyName, ele)) {
        return false;
      }
    }


    // ④-2 编辑属性
    switch (propertyName) {
      // 编辑数据类型
      case ViewElementConst.MDataType:
        return this.canEditDataType(ele);
      // 编辑数据对象类型
      case ViewElementConst.ObjectType:
        return this.canEditObjectType(ele);
      // 编辑是否多语,不显示编辑框
      case ViewElementConst.IsMultiLanguage:
        return false;
      // 编辑长度/精度，某些数据类型不可编辑
      case ViewElementConst.Length:
        return this.canEditLength(ele);
      case ViewElementConst.Precision:
        return this.canEditPresicion(ele);
      case ViewElementConst.DefaultValue:
        return this.canEditDefaultValue(ele);
      default:
        break;
    }
    return true;
  }

  /**
   * 是否可编辑数据类型(id类型字段不可修改数据类型)
   * @param dataItem 当前字段
   */
  static canEditDataType(dataItem: GSPViewModelElement): boolean {
    // 若为业务字段数据类型，则允许修改数据类型
    if (dataItem.IsUdt) {
      return true;
    }
    // 【原因】关联-数据类型不可修改；但是，当前是业务字段数据类型，需要修改为文本-36-0；所以应该在可以修改数据类型的控制那里，添加假设当前为业务字段，则可以修改数据类型；
    if (this.isIDType(dataItem)) {
      if (dataItem.MDataType !== GSPElementDataType.String) {
        return true;
      } else {
        // alert('ID类型字段数据类型不可修改。');
        return false;
      }
    }
    return true;
  }

  /**
   * 是否可编辑对象类型(id类型字段不可修改数据类型)
   * @param dataItem 当前字段
   */
  static canEditObjectType(dataItem: GSPViewModelElement): boolean {
    if (this.isIDElement(dataItem.ID) || this.isParentIDElement(dataItem.ID)) {
      return false;
    }
    return true;
  }

  /**
   * 是否可编辑字段长度(1.ID类型字段不可修改长度；2.某些数据类型不可修改长度；)
   * @param dataItem 当前字段
   */
  static canEditLength(dataItem: GSPViewModelElement): boolean {
    if (this.isIDType(dataItem)) {
      // alert('ID类型字段长度不可修改。');
      return false;
    }
    return this.dataTypes.find(item => item.value === dataItem.MDataType).canChangeLength;
  }

  /**
   * 是否可编辑字段长度(1.ID类型字段不可修改精度；2.某些数据类型不可修改精度；)
   * @param dataItem 当前字段
   */
  static canEditPresicion(dataItem: GSPViewModelElement): boolean {
    if (this.isIDType(dataItem)) {
      // alert('ID类型字段精度不可修改。');
      return false;
    }
    return this.dataTypes.find(item => item.value === dataItem.MDataType).canChangePresicion;
  }

  /**
   * 字段对象类型为关联/对象是否多语为true时，不允许赋默认值
   * @param dataItem
   */
  static canEditDefaultValue(dataItem: GSPViewModelElement): boolean {
    if (dataItem.ObjectType === GSPElementObjectType.Association) {
      return false;
    }
    if (dataItem.IsMultiLanguage) {
      return false;
    }
    return true;
  }

  /**
   * 编辑带有udt的字段
   * @param propertyName 属性名
   * @param element 字段
   */
  private static editElementWithUdt(propertyName: string, element: GSPViewModelElement): boolean {
    return true;
    // // 对象类型为[约束]，但是希望看到当前的选择，此时不可编辑
    // switch (propertyName) {
    //   case ViewElementConst.ObjectType:
    //     return true;
    //   case ViewElementConst.MDataType:
    //     return true;
    //   default:
    //     // if (!UdtUtil.canPropertyBeEdit(propertyName, (<GSPViewModelElement>element).UnifiedDataType)) {
    //     //     continueToEdit = false;
    //     //     break;
    //     // } else {
    //     return true;
    //   // }
    // }
  }

  /**
   * 是否id类型字段
   * 包含id,parentId,关联字段
   * @param dataItem 当前字段
   */
  static isIDType(dataItem: GSPViewModelElement) {
    if (this.isIDElement(dataItem.ID) || this.isParentIDElement(dataItem.ID)
      || dataItem.ObjectType === GSPElementObjectType.Association) {
      return true;
    }
    return false;
  }

  /**
   * 是否be带出字段
   * @param id
   */
  private static isVmVirtualEle(ele: GSPViewModelElement) {
    return ele.IsVirtualViewElement;
  }

  /**
   * 是否id字段
   * @param id
   */
  static isIDElement(id: string) {
    if (id === this.currentObject.PrimaryKey) {
      return true;
    }
    if (isNullOrUndefined(this.currentObject.ColumnGenerateID)) {
      return false;
    } else {
      return id === this.currentObject.ColumnGenerateID.ElementID;
    }

  }

  /**
   * 是否外键字段
   * @param id
   */
  static isParentIDElement(id: string) {
    if (isNullOrUndefined(this.currentObject.Keys) || this.currentObject.Keys.length === 0) {
      return false;
    } else {
      const assoKey = this.currentObject.Keys.find(key => key.SourceElement === id);
      return assoKey != null;
    }
  }

}



