import { GSP, Package } from '@farris/ide-devkit';

import { ComponentFactoryResolver, Injector } from '@angular/core';
import { VmDesignerPackageView } from './views';
import { VmDesignerFrameComponent } from './views/vmdesigner-frame/vm-designer-frame.component';

export class VmDesignerPackage extends Package {

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openUdt(uri));
  }

  openUdt(uri) {
    if (uri.endsWith('.vm') || uri.endsWith('.vo')) {
      const pkgView = new VmDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as VmDesignerFrameComponent;
      currentElement.metadataUri = uri;
      // 这里需要处理uri，title应返回元数据名称，
      return {
        getTitle: () => currentElement.getFileNameFromUri(uri),
        getElement: () => pkgView.getElement()
      };
    }
  }
}



