import { ViewModelActionType } from '../Enum/ViewModelActionType';
import { ViewModelMapping } from '../Common/ViewModelMapping';
import { ViewModelReturnValue } from './ReturnValue/ViewModelReturnValue';
import { ViewModelParameter } from './Para/ViewModelParameter';
import { Guid } from '../../ref';
import { ViewModelVoidReturnValue } from './ReturnValue/ViewModelVoidReturnValue';



/**
 * VM Action抽象类
 * @author wangjiegj<wangjiegj@inspur.com>
 */


/**
 * VM Action抽象类
 */
export abstract class ViewModelAction {

    constructor() {
        this.ID = Guid.newGuid();
        this.ReturnValue = new ViewModelVoidReturnValue();
    }

    /**
     * 唯一标志
     */
    public ID: string;

    /**
     * 编号
     */
    public Code: string;

    /**
     * 名称
     */
    public Name: string;

    /**
     * Action所在的节点
     */
    // public Owner: GSPViewObject;

    /**
     * 类型
     */
    public abstract Type: ViewModelActionType;

    /**
     * 形参集合
     */
    public ParameterCollection: ViewModelParameter[];

    /**
     * 返回值
     */
    public ReturnValue: ViewModelReturnValue;

    /**
     * 操作映射
     */
    public Mapping: ViewModelMapping;


    /**
     * 构件名称
     */
    ComponentName: string;
}



