import { Component, OnInit, OnChanges, SimpleChanges, Input, ViewChild } from '@angular/core';
import { ViewActionList, ViewActionBase } from '../util/vm-reference';
import { isNullOrUndefined } from 'util';
import { BaseOperationGridComponent } from './base-operation-grid/base-operation-grid.component';

/**
 * 操作编辑界面基类（左列右卡）
 * 在子类中重写新增方法（新增后手动调用super.refresh()方法）
 * 列表可复用
 * 卡片以Action为例，Validation由于表单控件不同，需重写
 */
@Component({
  selector: 'app-base-operation-component',
  templateUrl: './base-operation-component.component.html',
  styleUrls: ['./base-operation-component.component.css']
})
export class BaseOperationComponentComponent implements OnInit, OnChanges {

  @ViewChild('grid') validationGrid: BaseOperationGridComponent;

  /**
   * 列表数据源
   */
  @Input() viewOperations: ViewActionList;

  /**
   * 卡片数据源
   */
  viewOperation: ViewActionBase;

  constructor() { }
  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.refresh();
  }

  load() {
    this.refresh();
  }

  protected refresh() {
    if (isNullOrUndefined(this.viewOperations) || isNullOrUndefined(this.viewOperations.toJS())) {
      this.viewOperation = null;
      return;
    }

    if (isNullOrUndefined(this.viewOperation) || !this.viewOperations.contains(this.viewOperation)) {
      // 默认选中第一个
      this.viewOperations.setSelectIndex(0);
      this.viewOperation = this.viewOperations.currentSelectItem;
    }

    this.viewOperations.selectNewItem.subscribe(
      value => {
        this.viewOperation = this.viewOperations.currentSelectItem;
      }
    );
  }

  remove() {
    this.validationGrid.remove();
  }
}



