import { Component, OnInit, Input, OnChanges, SimpleChanges, ViewChild  } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { AuthData } from '../util/auth-data';
import { SecurityEntryEntity } from '../security-entry/entity/security-entry.entity';


@Component({
  selector: 'app-auth-op-tab',
  templateUrl: './auth-op-tab.component.html',
  styleUrls: ['./auth-op-tab.component.css']
})
export class AuthOpTabComponent implements OnInit, OnChanges {
  @ViewChild('securityEntryAuthOpComponent') securityEntryAuthOpComponent;
  @Input() actions: AuthData[];
  @Input() elements: AuthData[];
  @Input() securityEntry: SecurityEntryEntity;

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  save() {
    this.securityEntryAuthOpComponent.save();
  }
}



