import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { AuthorizationEntity } from '../../entity/authorizationentity';
import { AuthorizationTreeComponent } from '../authorization-tree/authorization-tree.component';
import { isNullOrUndefined } from 'util';
import { ModalDirective } from '@farris/ui/container/modal/modal';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'authorization-select',
  templateUrl: './authorization-select.component.html',
  styleUrls: ['./authorization-select.component.css']
})
export class AuthorizationSelectComponent implements OnInit, OnChanges {
  @ViewChild('autoShownModal') autoShownModal: ModalDirective;
  @ViewChild('treeDialog') treeDialog: AuthorizationTreeComponent;
  // @Input()
  @Input() selectedIdsIn: string[];
  @Input() triggerShowWindow: boolean;
  isModalShown = false;
  authorizationsSelected: AuthorizationEntity[];

  @Output() authorizations: EventEmitter<AuthorizationEntity[]> = new EventEmitter<AuthorizationEntity[]>();
  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 初次加载不显示
    if (isNullOrUndefined(changes.triggerShowWindow) || isNullOrUndefined(changes.triggerShowWindow.previousValue)) {
      return;
    }
    if (!this.isModalShown) {
      this.showModal();
    }
  }

  showModal(): void {
    this.isModalShown = true;
  }

  hideModal(): void {
    this.autoShownModal.hide();
    this.isModalShown = false;
  }

  onHidden(): void {
    this.isModalShown = false;
  }
  btnOkClick() {
    this.authorizationsSelected = this.treeDialog.dialogOk();
    this.hideModal();
    this.authorizations.next(this.authorizationsSelected);
  }

  btnClearClick() {
    this.authorizationsSelected = null;
    this.hideModal();
    this.authorizations.next(this.authorizationsSelected);
  }
}



