import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { HttpUrlConst } from '../util/vm-reference';
import { CacheService } from '@farris/ide-devkit/services';

/**
 * be发布按钮服务
 */
@Injectable()
export class VmPublishService {

    private currentUrl = HttpUrlConst.currentUrl;

    constructor(private http: Http, private cache: CacheService) {
    }
    private sessionId = this.cache.get('sessionId');
    private urlBase = `${this.currentUrl}`
        + `/api/dev/main/v1.0/viewmodel`;
    private generateUrl = this.urlBase;
    private publishServiceUrl = this.urlBase + '/publish';
    private saveAndSysnUrl = this.urlBase + '/sysn';
    private buildUrl = this.urlBase + '/build';

    /**
     * 发布
     */
    generate(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const headers = new Headers();
        headers.append('sessionId', this.sessionId);

        const options = new RequestOptions({
            params: {},
            headers: headers
        });
        return this.http.put(this.generateUrl, jitData, options);
    }


    /**
     * 发布服务
     */
    publishService(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const headers = new Headers();
        headers.append('sessionId', this.sessionId);

        const options = new RequestOptions({
            params: {},
            headers: headers
        });
        return this.http.put(this.publishServiceUrl, jitData, options);
    }


    /**
     * 保存并同步
     */
    saveAndSysnViewModel(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;

        const headers = new Headers();
        headers.append('sessionId', this.sessionId);

        const options = new RequestOptions({
            params: {},
            headers: headers
        });
        return this.http.put(this.saveAndSysnUrl, jitData, options);
    }


    /**
     * 编译
     */
    buildViewModel(fileName: string, path: string): Observable<any> {
        const jitData = new JitMetadataContent();
        jitData.name = fileName;
        jitData.path = path;
        const sessionId1 = this.cache.get('sessionId');
        const headers = new Headers();
        headers.append('sessionId', sessionId1);

        const options = new RequestOptions({
            params: {},
            headers: headers
        });
        return this.http.put(this.buildUrl, jitData, options);
    }
}

/**
 * jit实体结构
 */
class JitMetadataContent {
    name: string;
    path: string;
}



