import {
  Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter,
  SimpleChanges, Renderer2, Injector, ComponentFactoryResolver
} from '@angular/core';
import { FormArray, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

import { AddEvent, EditEvent, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';

import { ModalDirective } from '@farris/ui/container/modal/modal/modal.directive';
import { BsModalService } from '@farris/ui';

import { FuncOperationEntity } from '../entity/func-operation.entity';
import { SecurityEntryEntity } from '../entity/security-entry.entity';
import { OpRelationEntity } from '../entity/op-relatioin.entity';

import { SecurityEntryService } from '../service/security-entry.service';
import { isNullOrUndefined } from 'util';

import { FuncOpSelectComponent } from '../security-entry-func-op/func-op-select/func-op-select.component';


@Component({
  selector: 'app-security-entry-func-op',
  templateUrl: './security-entry-func-op.component.html',
  styleUrls: ['./security-entry-func-op.component.css']
})
export class SecurityEntryFuncOpComponent implements OnInit, OnChanges {

  @Input() actions: Array<any> = [];
  @Input() securityEntry: SecurityEntryEntity;

  public selectedKeys: any[] = [];
  public selectableSettings: SelectableSettings = {
    checkboxOnly: false,
    mode: 'single'
  };

  saveEmitter: EventEmitter<SecurityEntryEntity> = new EventEmitter<SecurityEntryEntity>();

  public currentItem: any;

  public formGroup: FormGroup;
  public formGroups: FormGroup = new FormGroup({ items: new FormArray([]) });
  private editedRowIndex: number;

  constructor(
    private securityEntryervice: SecurityEntryService,
    private formBuilder: FormBuilder,
    private renderer: Renderer2,
    private modalService: BsModalService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector) { }
  // 功能操作相关
  operationSelected: FuncOperationEntity;
  datas: Array<OpRelationEntity>;

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.init();
  }

  init() {

    if (isNullOrUndefined(this.securityEntry)) {
      return;
    }

    if (this.securityEntry && this.securityEntry.opRelations) {
      this.buildData();
      return; // 第一次加载界面时，为undefined，加载一次赋值后，这里就不会出现undefined的情况了
    }


    this.securityEntryervice.getFuncSecurityEntry(this.securityEntry).subscribe(item => {
      if (isNullOrUndefined(this.securityEntry)) {
        return;
      } else {
        if (item) {
          this.securityEntry.id = item.id; // 如果场景已经存在，则将获取场景ID
          this.securityEntry.opRelations = item.opRelations;
          if (isNullOrUndefined(this.securityEntry.opRelations)) {
            this.securityEntry.opRelations = [];
          }
        } else {
          if (isNullOrUndefined(this.securityEntry.opRelations)) {
            this.securityEntry.opRelations = [];
          }
        }
        this.buildData();
      }
    });
  }

  buildData() {
    this.datas = this.securityEntry.opRelations;
    this.actions.map(action => {
      const myitem = this.securityEntry.opRelations.find(entry => entry.actionId === action.id);
      if (myitem) {
        myitem.actionCode = action.code;
        myitem.actionName = action.name;
      } else {
        const newRelation = new OpRelationEntity();
        newRelation.actionId = action.id;
        newRelation.actionName = action.name;
        this.securityEntry.opRelations.push(newRelation);
      }
    });
  }



  /**
   * 开始编辑
   */
  private cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, type } = args;
    // 若为新增行，不响应
    if (rowIndex === -1) {
      return;
    }
    this.currentItem = dataItem;
    if (column.field === 'operationName') {
      this.ShowOperationDialog();
    }

  }

  private createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'relationId': [dataItem.relationId],
      'actionId': [dataItem.actionId],
      'operationId': [dataItem.operationId],
      'operationCode': [dataItem.operationCode],
      'operationName': [dataItem.operationName]
    });
  }

  /**
  * 编辑结束
  *
  */
  private cellCloseHandler(args: any) {
    const { sender, formGroup, dataItem, rowIndex, column } = args;
    let item = this.securityEntry.opRelations.find(x => x.entryId === dataItem['actionId']);
    if (isNullOrUndefined(item)) {
      item = new OpRelationEntity();
    }
    const index = this.securityEntry.opRelations.indexOf(item);
    const propertyName = column.field;
    const newValue = formGroup.value[propertyName];

    switch (column.field) {
      case 'actionId':
        item.actionId = newValue;
        break;
      case 'operationId':
        item.operationId = newValue;
        break;
    }
  }


  /**
  * 加载功能操作
  *
  */
  ShowOperationDialog() {
    const compFactory = this.resolver.resolveComponentFactory(FuncOpSelectComponent);
    const compRef = compFactory.create(this.injector);
    // compRef.instance.metadataPath = HttpUrlConst.currentPath;
    // compRef.instance.typeName = this.typeName;
    compRef.instance.selectOperationEmitter.subscribe(data => {
      console.log('$event', data);
      dialog.close();
      this.operationSelected = data;
      this.currentItem.operationId = data.id;
      this.currentItem.operationName = data.name;
      this.currentItem.operationCode = data.code;
    });
    const modalConfig = {
      title: '操作选择',
      width: 400,
      height: 500,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe($event => {
      dialog.close();
    });
  }

  /**
   * 保存权限扩展信息
   *
   */
  save() {
    console.log(this.securityEntry);
    this.securityEntryervice.saveFuncSecurityEntry(this.securityEntry).subscribe(item => {
      this.securityEntry.id = item.id;
      this.saveEmitter.next(this.securityEntry);
    });
  }
}



