/**
 * 映射类型枚举
 * @author wangjiegj<wangjiegj@inspur.com>
 */

/**
 * 映射类型枚举
 */
enum MappingType {

    /**
     * BE
     */
    BizEntity = 'BizEntity',

    /**
     * DM
     */
    DataModel = 'DataModel',

    /**
     * BE节点
     */
    BEObject = 'BEObject',

    /**
     * GspDataModel节点
     */
    DMObject = 'DMObject',

    /**
     * 节点元素
     */
    Element = 'Element',

    /**
     * BE操作
     */
    BizOperation = 'BizOperation',

    /**
     * 构件方法
     */
    ComponentMethod = 'ComponentMethod',

    /**
     * 数据对象
     */
    DataObject = 'DataObject',

    /**
     * 数据对象列
     */
    GSPColumn = 'GSPColumn',

}

export { MappingType };

export const MappingTypes: Array<{ text: string, value: MappingType }> = [
    { text: '业务实体', value: MappingType.BizEntity },
    { text: '业务实体节点', value: MappingType.BEObject },
    { text: '业务实体字段', value: MappingType.Element },
    { text: '业务实体操作', value: MappingType.BizOperation },
    { text: '构件方法', value: MappingType.ComponentMethod },
];



