import { GSPViewModelElement, GSPBizEntityElement } from './libs';

export class BizElementToViewElementUtil {

    /**
     * 关联引用字段，映射be字段为vmElement,记录到asso.RefElementCollection中
     * @param element be字段
     * @param vEle 业务字段
     */
    static mapBizElementToVmElement(element: GSPBizEntityElement, vEle: GSPViewModelElement): any {
        vEle.Code = element.Code;
        vEle.Name = element.Name;
        vEle.LabelID = element.LabelID;
        vEle.Length = element.Length;
        vEle.Precision = element.Precision;
        vEle.ObjectType = vEle.ObjectType;
        vEle.MDataType = element.MDataType;
        vEle.DefaultValue = element.DefaultValue;
        vEle.IsRequire = element.IsRequire;
    }
}



