import { List } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../../base/listBase';
import { ViewParameterBase, ViewBizActionParameter, ViewBizMgrActionParameter } from './viewParameter';

import { ViewModelParameter, MappedBizActionParameter, MappedCdpActionParameter } from '../../../ref';

export abstract class ViewParameterListBase extends ListBase<ViewParameterBase> {

    constructor(paras: ViewModelParameter[] | List<any>) {
        super(paras);
        if (isNullOrUndefined(paras)) {
            return;
        }
        this.data = this.getViewParameters(this.data);
    }

    private getViewParameters(paras: List<any>): List<ViewParameterBase> {
        if (isNullOrUndefined(paras)) {
            return;
        }

        let paraList = paras;

        if (paraList.size > 0) {
            for (let i = 0; i < paraList.size; i++) {
                paraList = paraList.update(i, () => this.getViewParameter(paraList.get(i)));
            }
            return paraList;
        }
    }

    protected abstract getViewParameter(para: any);
}

export class ViewBizActionParaList extends ViewParameterListBase {

    constructor(paras: MappedBizActionParameter[] | List<any>) {
        super(paras);
    }

    protected getViewParameter(para: any) {
        return new ViewBizActionParameter(para);
    }
}

export class ViewCdpActionParaList extends ViewParameterListBase {

    constructor(paras: MappedCdpActionParameter[] | List<any>) {
        super(paras);
    }

    protected getViewParameter(para: any) {
        return new ViewBizMgrActionParameter(para);
    }
}






