import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { AuthorizationEntity } from '../../entity/authorizationentity';
import { AuthoperationTreeComponent } from '../authoperation-tree/authoperation-tree.component';
import { isNullOrUndefined } from 'util';

import { AuthoperationEntity } from '../../entity/authoperationentity';
import { ModalDirective } from '@farris/ui/container/modal/modal';

@Component({
  selector: 'app-authoperation-select',
  templateUrl: './authoperation-select.component.html',
  styleUrls: ['./authoperation-select.component.css']
})
export class AuthoperationSelectComponent implements OnInit, OnChanges {
  @ViewChild('autoShownModal') autoShownModal: ModalDirective;
  @ViewChild('treeDialog') treeDialog: AuthoperationTreeComponent;

  // @Input() ；
  @Input() triggerShowWindow: boolean;
  // 要展示的数据操作所隶属的权限对象
  @Input() authorizationId: string;
  // 要展示的数据操作所关联的的权限字段
  @Input() authfield: string;

  isModalShown = false;
  operationSelected: AuthoperationEntity;

  // 选中的数据操作实体
  @Output() authoperation: EventEmitter<AuthoperationEntity> = new EventEmitter<AuthoperationEntity>();
  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 初次加载不显示
    if (isNullOrUndefined(changes.triggerShowWindow) || isNullOrUndefined(changes.triggerShowWindow.previousValue)) {
      return;
    }
    if (!this.isModalShown) {
      this.showModal();
    }
  }

  showModal(): void {
    this.isModalShown = true;
  }

  hideModal(): void {
    this.autoShownModal.hide();
  }

  onHidden(): void {
    this.isModalShown = false;
  }

  btnOkClick() {
    this.operationSelected = this.treeDialog.dialogOk();
    this.hideModal();
    this.authoperation.next(this.operationSelected);
  }


  btnClearClick() {
    this.operationSelected = null;
    this.hideModal();
    this.authoperation.next(this.operationSelected);
  }

}





