import { MethodparamListComponent } from './methodparam-list.component';
import {
  Component, OnInit, OnChanges
} from '@angular/core';
import {
  ViewParameterListBase, ViewParameterBase,
  MappedCdpActionParameter, ViewBizMgrActionParameter, ViewCdpActionParaList
} from '../../../util/vm-reference';

@Component({
  selector: 'app-cdp-action-methodparam-list',
  templateUrl: './methodparam-list.component.html',
  styleUrls: ['./methodparam-list.component.css']
})
export class CdpActionMethodparamListComponent extends MethodparamListComponent implements OnInit, OnChanges {

  createNewViewParameters(): ViewParameterListBase {
    const paras = new Array<MappedCdpActionParameter>();
    return new ViewCdpActionParaList(paras);
  }

  /**
   * 创建构件方法的新参数
   */
  createViewParaInstance(para: MappedCdpActionParameter): ViewParameterBase {
    return new ViewBizMgrActionParameter(para);
  }

  createParaInstance(): MappedCdpActionParameter {
    return new MappedCdpActionParameter();
  }
}



