import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { AuthorizationService } from '../../service/authorization.service';
import { AuthorizationEntity } from '../../entity/authorizationentity';
import { isNullOrUndefined } from 'util';


@Component({
  // tslint:disable-next-line:component-selector
  selector: 'authorization-tree',
  templateUrl: './authorization-tree.component.html',
  styleUrls: ['./authorization-tree.component.css']
})
export class AuthorizationTreeComponent implements OnInit {
  files: any;
  checkedKeys: any;
  authorizationEntity: AuthorizationEntity;
  authorizationsSelect: AuthorizationEntity[];
  datas = new Array<AuthorizationEntity>();
  treeElement: AuthorizationEntity;

  // @Input
  @Input() selectedIdsIn: string[];


  constructor(private authorizationService: AuthorizationService) { }

  ngOnInit() {
    this.checkedKeys = [];
    this.files = [];
    this.treeElement = new AuthorizationEntity();
    this.authorizationService.GetAuthorizationTree().subscribe(data => {
      console.log(data);
      data.forEach(x => {
        if (isNullOrUndefined(x.parentId)) {
          x.parentId = undefined;
        }
        this.datas.push(x);
      });
    });
    if (this.selectedIdsIn) {
      this.selectedIdsIn.forEach(x => {
        if (x.length > 0) {
          const obj = this.datas.find((auth: AuthorizationEntity) => auth.id === x);
          // if (obj.type.toString() === '1') {
          //    // TODO 业务对象Id可能和权限对象Id一致,这里和业务对象的关系还需要处理
          // }
          this.checkedKeys.push(x);
        }
      });
    }
  }

  // 弹出框 分配的功能操作控件特性
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'multiple'
    };
  }

  // 弹出框 分配的功能操作控件图标样式
  iconClass(): any {
    return {
      'k-i-gear': true,
      'k-icon': true
    };
  }


  dialogOk() {
    this.authorizationsSelect = new Array<AuthorizationEntity>();
    this.checkedKeys.forEach(x => {
      const obj = this.datas.find((auth: AuthorizationEntity) => auth.id === x);
      if (obj.type.toString() === '1') {
        const item = new AuthorizationEntity();
        item.id = x;
        item.name = obj.name;
        this.authorizationsSelect.push(item);
      }
    });
    return this.authorizationsSelect;
  }

}



