import { AuthData } from './auth-data';
import { BizOperation, GSPBizEntityElement, IGSPCommonElement } from '../../../util/vm-reference';

export class ConvertToAuthDatas {
  /**
   * mgrAction转换为操作权限需要格式
   */
  static convertMgrActionsToAuthDatas(ops: BizOperation[]): AuthData[] {
    const funcOpActions = new Array<AuthData>();
    if (ops && ops.length > 0) {
      ops.forEach(
        op => {
          const action = this.convertMgrActionToAuthData(op);
          funcOpActions.push(action);
        }
      );
    }
    return funcOpActions;
  }

  private static convertMgrActionToAuthData(op: BizOperation): AuthData {
    const funcOpAction = new AuthData();
    funcOpAction.id = op.Code;
    funcOpAction.code = op.Code;
    funcOpAction.name = op.Name;
    return funcOpAction;
  }

  /**
   * 字段转换为数据权限需要格式
   */
  static convertElementsToFuncOpActions(eles: IGSPCommonElement[]): AuthData[] {
    const funcOpActions = new Array<AuthData>();
    if (eles && eles.length > 0) {
      eles.forEach(
        op => {
          const action = this.convertElementToFuncOpAction(op);
          funcOpActions.push(action);
        }
      );
    }
    return funcOpActions;
  }

  private static convertElementToFuncOpAction(ele: IGSPCommonElement): AuthData {
    const funcOpAction = new AuthData();
    funcOpAction.id = ele.LabelID;
    funcOpAction.code = ele.LabelID;
    funcOpAction.name = ele.Name;
    return funcOpAction;
  }
}



