import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { isNullOrUndefined, isNull } from 'util';

import { AuthorizationEntity } from '../../../../authorization/entity/authorizationentity';
import { AuthEntryEntity } from '../../../../authorization/entity/authentryentity';
import { AuthFieldEntity } from '../../../../authorization/entity/authfieldentity';
import { AuthorizationService } from '../../../../authorization/service/authorization.service';

import { SecurityEntryService } from '../../../service/security-entry.service';

@Component({
  selector: 'app-auth-field-select',
  templateUrl: './auth-field-select.component.html',
  styleUrls: ['./auth-field-select.component.css']
})
export class AuthFieldSelectComponent implements OnInit {

  authorizationIds: string[];
  authfield: string;
  treeAuthorization: AuthorizationEntity;
  treeEntity: AuthEntryEntity;

  closeModalEmitter: EventEmitter<any> = new EventEmitter<AuthFieldEntity>();
  selectOperationEmitter: EventEmitter<AuthFieldEntity> = new EventEmitter<AuthFieldEntity>();
  operationSelected: AuthFieldEntity;
  ops: AuthFieldEntity[] = [];
  expandedKeys: string[] = [];

  constructor(
    private authorizationService: AuthorizationService,
    private securityEntryervice: SecurityEntryService) { }

  ngOnInit() {
    this.authorizationService.GetAuthfields(this.authorizationIds).subscribe(data => {
      data.forEach(item => {
        // 加载权限对象
        this.treeAuthorization = item;
        const authObj = new AuthFieldEntity(); // 权限对象 和权限字段id有可能重复，权限对象和业务对象id也有可能重复
        authObj.id = item.id;
        authObj.code = item.code;
        authObj.name = item.name;
        authObj.type = '0';
        authObj.children = [];
        if (!isNullOrUndefined(this.authorizationIds)) {
          const find = this.authorizationIds.find(id => id === this.treeAuthorization.id);
          if (isNullOrUndefined(find)) {
            return;
          }
        }

        // 加载权限字段
        const authEntries = this.treeAuthorization.authEntry;
        if (isNullOrUndefined(authEntries) || authEntries.length === 0) {
          return;
        }
        this.expandedKeys.push(authObj.id); // 默认展开
        authEntries.forEach(child => {
          this.treeEntity = child;
          const authField = new AuthFieldEntity(); // 权限对象 和权限字段id有可能重复，权限对象和业务对象id也有可能重复
          authField.id = this.treeEntity.authField;
          authField.code = this.treeEntity.authFieldCode;
          authField.name = this.treeEntity.authFieldName;
          authField.type = '1';
          authField.authorizationId = authObj.id;
          authField.authorizationCode = authObj.code;
          authField.authorizationName = authObj.name;
          authObj.children.push(authField);
        });
        this.ops.push(authObj);
      });
    });
  }

  public hasChildren = (item: any) => item.children && item.children.length > 0;
  public fetchChildren = (item: any) => of(item.children);

  hideModal(): void {
    this.closeModalEmitter.next();
    this.selectOperationEmitter.next(this.operationSelected);
  }

  // 弹出框 分配的功能操作控件特性
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'single'
    };
  }

  // 弹出框 分配的功能操作控件图标样式
  iconClass({ type }: any): any {
    return {
      'k-i-folder': type.toString() !== '1',
      'k-i-gear': type.toString() === '1',
      'k-icon': true
    };
  }

  btnOkClick() {
    this.hideModal();
  }

  btnClearClick() {
    this.operationSelected = null;
    this.hideModal();
  }

  // 切换事件
  handleSelection({ dataItem }: any): void {
    this.operationSelected = dataItem;
  }
}





