import {
  Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter,
  SimpleChanges, Renderer2, Injector, ComponentFactoryResolver
} from '@angular/core';
import { FormArray, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

import { AddEvent, EditEvent, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';

import { ModalDirective } from '@farris/ui/container/modal/modal/modal.directive';
import { BsModalService } from '@farris/ui';

import { isNullOrUndefined } from 'util';

import { SecurityEntryEntity } from '../../entity/security-entry.entity';
import { AuthFieldEntryEntity } from '../../entity/auth-field-entry.entity';
import { SecurityEntryService } from '../../service/security-entry.service';
import { AuthFieldSelectComponent } from './auth-field-select/auth-field-select.component';

import { AuthorizationEntity } from '../../../authorization/entity/authorizationentity';
import { AuthFieldEntity } from '../../../authorization/entity/authfieldentity';


import { AuthEntryEntity } from '../../../authorization/entity/authentryentity';

@Component({
  selector: 'app-auth-field-grid',
  templateUrl: './auth-field-grid.component.html',
  styleUrls: ['./auth-field-grid.component.css']
})
export class AuthFieldGridComponent implements OnInit, OnChanges {


  @Input() securityEntry: SecurityEntryEntity;

  currentAuthFieldEntry: AuthFieldEntryEntity;

  datas: any[];

  public currentItem: any;
  public selectedKeys: any[] = [];
  public formGroup: FormGroup;
  public formGroups: FormGroup = new FormGroup({ items: new FormArray([]) });
  private editedRowIndex: number;

  constructor(
    private securityEntryService: SecurityEntryService,
    private formBuilder: FormBuilder,
    private renderer: Renderer2,
    private modalService: BsModalService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector) { }
  // 权限字段
  authFieldSelected: AuthFieldEntity;

  ngOnInit() {
    this.load();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.load();
  }

  load() {
    if (this.securityEntry) {
      this.datas = this.securityEntry.authFields;
    }
  }


  // 已经选中的权限对象标识集合
  get authorizationIds(): string[] {
    const authobjs = this.securityEntry.authObjIds;
    if (isNullOrUndefined(authobjs)) {
      return null;
    }
    const authorizationIds = new Array<string>();
    return authobjs.split(',');
  }

  /**
   * 开始编辑
   */
  private cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, type } = args;
    // 若为新增行，不响应
    if (rowIndex === -1) {
      return;
    }
    this.currentItem = dataItem;

    if (column.field === 'authFieldName') {
      this.showFieldDialog();
    } else {
      return;
    }

    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
    }

  }

  private createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'id': [dataItem.id],
      'authObjId': [dataItem.authObjId],
      'authObjName': [dataItem.authObjName],
      'authFieldId': [dataItem.authFieldId],
      'authFieldCode': [dataItem.authFieldCode],
      'authFieldName': [dataItem.authFieldName]
    });
  }

  /**
  * 编辑结束
  *
  */
  private cellCloseHandler(args: any) {
    const { sender, formGroup, dataItem, rowIndex, column } = args;
    let item = this.datas.find(x => x.fieldId === dataItem['fieldId']);
    if (isNullOrUndefined(item)) {
      item = new AuthFieldEntryEntity();
    }
    const index = this.datas.indexOf(item);
    const propertyName = column.field;
    const newValue = formGroup.value[propertyName];

    switch (column.field) {
      case 'authFieId':
        item.authFieldId = newValue;
        break;
    }
  }


  /**
  * 加载功能操作
  *
  */
  showFieldDialog() {
    const compFactory = this.resolver.resolveComponentFactory(AuthFieldSelectComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.authorizationIds = this.authorizationIds;
    compRef.instance.selectOperationEmitter.subscribe(data => {
      dialog.close();
      this.authFieldSelected = data;
      this.currentItem.authObjId = data.authorizationId;
      this.currentItem.authObjName = data.authorizationName;
      this.currentItem.authFieldId = data.id;
      this.currentItem.authFieldCode = data.code;
      this.currentItem.authFieldName = data.name;
    });
    const modalConfig = {
      title: '权限字段选择',
      width: 400,
      height: 500,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe($event => {
      dialog.close();
    });
  }


  onSelectedKeysChange(args: any) {
    this.currentAuthFieldEntry = this.datas.find(x => x.fieldId === this.selectedKeys[0]);
  }

  // /**
  //  * 保存权限扩展信息
  //  *
  //  */
  // save() {
  //   this.securityEntry.authFields = this.datas;
  //   // this.securityEntryervice.saveAuthSecurityEntry(this.securityEntry).subscribe(item => {
  //   //   this.securityEntry.id = item.id;
  //   //   this.saveEmitter.next(this.securityEntry);
  //   // });
  // }
}




