import { Injector, Injectable } from '@angular/core';
import { List, Map, fromJS } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import {
    ViewModel, GSPViewModel, ViewObject,
    ViewObjectList, GSPViewObject, Guid,
    GSPAssociationKey, GSPViewModelElement, IGSPCommonElement, GSPElementDataType, GSPElementObjectType, ViewModelConst, ViewObjectConst
} from '../util/vm-reference';
import { InitBizObject } from '../service/init/initialBizObject';
import { InitChildObject } from '../service/init/initChildObject';

@Injectable()
export class ObjectTreeService {

    /**
     * 当前be的不可变数据结构
     */
    private viewModel: ViewModel;
    private currentIndex: string;
    /**
    * 选中节点改变事件，发出新选中的节点
    */
    changeSelectObject: Subject<ViewObject> = new Subject<ViewObject>();
    currentSelectItem: Subject<ViewObject | ViewModel> = new Subject<ViewObject | ViewModel>();
    refreshView: Subject<ViewModel> = new Subject<ViewModel>();

    public resetViewModel(vm: ViewModel) {
        this.viewModel = vm;
    }

    constructor() {
    }

    toJS(): GSPViewModel {
        return this.viewModel.toJS();
    }

    // get isBizEntityNodeSelected(): viewModel {
    //     // if
    // }

    /**
     * 当前对象
     * 若为BE节点，则返回主对象信息
     */
    get currentObject(): ViewObject {
        const currentObjectPath = this.convertObjectIndexToPath(this.currentSelectObjectIndex);
        if (this.isBENode(currentObjectPath)) {
            const vo = this.viewModel.mainObject;
            return vo;
        } else {
            const vo = this.viewModel.getIn(currentObjectPath);
            return vo;
        }
    }

    /**
     * 根据对象树索引构造immutable.getIn的参数
     */
    private convertObjectIndexToPath(index: string) {
        const pathArray = new Array<string>();
        const indexArray = index.split('_');
        for (let i = 0; i < indexArray.length; i++) {
            // 第一层是BE节点,相当于当前对象为主对象节点
            if (i === 0) {
                // pathArray.push('0');
            } else if (i === 1) {
                // 第二层是主对象节点
                pathArray.push(ViewModelConst.MainObject);
            } else {
                pathArray.push(...[ViewObjectConst.ContainChildObjects, indexArray[i]]);
            }
        }
        return pathArray;
    }


    /**
     * 当前选中的对象节点索引
     * 初始化时为主对象索引0_0
     */
    get currentSelectObjectIndex(): string {
        if (this.currentIndex) {
            return this.currentIndex;
        } else {
            this.currentIndex = '0_0';
            return this.currentIndex;
        }
    }


    /**
     * 新增子对象
     */
    public addChildObject() {
        const currentObjectPath = this.convertObjectIndexToPath(this.currentSelectObjectIndex);
        if (this.isBENode(currentObjectPath)) {
            alert('当前节点不允许添加子级对象。');
            return;
        }
        const parentPath = currentObjectPath;
        const newIndex = this.addViewObjectByParentPath(parentPath);
        this.selectNewChildObject(newIndex);
    }

    /**
     * 新增同级对象
     */
    public addSiblingObject() {
        const currentObjectPath = this.convertObjectIndexToPath(this.currentSelectObjectIndex);
        if (this.isBENode(currentObjectPath) || this.isMainObjectNode(currentObjectPath)) {
            alert('当前节点不允许添加同级对象。');
            return;
        }
        // 获取父节点路径=当前节点路径向上2级
        const parentPath = currentObjectPath;
        parentPath.pop();
        parentPath.pop();

        const newIdex = this.addViewObjectByParentPath(parentPath);
        this.selectNewSiblingObject(newIdex);
    }

    /**
     * 删除当前选中对象
     */
    public deleteObject() {
        if (this.currentObject) {
            const obj = this.currentObject.toJS() as GSPViewObject;
            if (this.isDbeObject(obj)) {
                alert('依赖实体带出对象不允许删除。');
                return;
            }
        }
        const currentObjectPath = this.convertObjectIndexToPath(this.currentSelectObjectIndex);
        if (this.isBENode(currentObjectPath) || this.isMainObjectNode(currentObjectPath)) {
            alert('当前节点不允许删除。');
            return;
        }
        const currentObjectChildrenList = this.currentObject.get(ViewObjectConst.ContainChildObjects) as ViewObjectList;
        if (!isNullOrUndefined(currentObjectChildrenList) && currentObjectChildrenList.length > 0) {
            alert('当前对象包含子对象，请先删除子对象。');
            return;
        }

        // 获取子节点列表路径=当前节点路径向上1级
        const chidrenPath = currentObjectPath;
        const index = chidrenPath.pop();
        const currentObjectIndex = this.currentSelectObjectIndex;

        this.removeViewObjectByChidrenListPath(chidrenPath, +index);
        this.refreshViewLayer();
        this.selectPreObject(currentObjectIndex);
    }

    private selectNewChildObject(childIndex: number) {
        const currentIndex = this.currentSelectObjectIndex;
        const indexArray = currentIndex.split('_');
        const count = indexArray.length;
        if (count < 2) {
            return; // BE节点无新增，不处理
        } else {
            const newIndex = currentIndex.concat('_').concat(childIndex.toString());
            this.selectObjectByIndex(newIndex);
        }
    }
    private selectNewSiblingObject(childIndex: number) {
        const currentIndex = this.currentSelectObjectIndex;
        const indexArray = currentIndex.split('_');
        const count = indexArray.length;
        if (count < 3) {
            return; // BE节点、主节点无同级新增，不处理
        } else {
            const newIndex = currentIndex.slice(0, currentIndex.length - 1).concat(childIndex.toString());
            this.selectObjectByIndex(newIndex);
        }
    }

    /**
     * 删除成功后，选中前序子对象
     * 若无前序子对象，选中父对象
     */
    private selectPreObject(currentIndex: string) {
        let newIndex = '';
        const indexArray = currentIndex.split('_');
        const count = indexArray.length;
        if (count < 3) {
            return; // BE节点/主节点不处理
        } else {
            const lastNumber = +indexArray[count - 1];
            if (lastNumber > 0) {
                // 子节点前面有节点，则选中上一个
                newIndex = currentIndex.slice(0, currentIndex.length - 1).concat((lastNumber - 1).toString());
            } else {
                // 子节点前面无节点，选中父节点
                newIndex = currentIndex.slice(0, currentIndex.length - 2);
            }
            this.selectObjectByIndex(newIndex);
        }
    }



    public selectMainObject() {
        this.currentIndex = '0_0';
        this.changeSelectObject.next(this.currentObject);
        this.currentSelectItem.next(this.currentObject);
    }

    /**
     * 选中新节点
     * @param index
     */
    public selectObjectByIndex(index: string) {
        this.currentIndex = index;
        this.changeSelectObject.next(this.currentObject);

        // 基本信息页信息源
        const currentObjectPath = this.convertObjectIndexToPath(this.currentSelectObjectIndex);
        if (this.isBENode(currentObjectPath)) {
            this.currentSelectItem.next(this.viewModel);
            currentObjectPath.push(ViewModelConst.MainObject);
        } else {
            this.currentSelectItem.next(this.currentObject);
        }
    }

    /**
     * 根据子对象list的路径添加新对象Map
     * @param path
     */
    private addViewObjectByParentPath(path: string[]): number {
        const parentObject = this.viewModel.getIn(path) as ViewObject;
        const newViewObject = this.getNewViewObject(parentObject);
        parentObject.addChildObject(newViewObject);
        this.refreshViewLayer();
        return parentObject.childrenObjects.getIndexByID(newViewObject.ID);
    }

    /**
     * 刷新视图层
     */
    public refreshViewLayer() {
        this.refreshView.next(this.viewModel);
    }

    /**
     * 根据子对象list的路径删除特定索引的对象Map
     * @param path 当前路径
     * @param index 删除第index个子对象
     */
    private removeViewObjectByChidrenListPath(path: string[], index: number) {
        const list = this.viewModel.getIn(path) as ViewObjectList;
        list.remove(index);
    }

    private getNewViewObject(parentViewObject: ViewObject): ViewObject {
        if (isNullOrUndefined(parentViewObject)) {
            alert('当前父节点为空。');
            return;
        }
        return new ViewObject(InitChildObject.initChildObject(parentViewObject.toJS()));
    }

    /**
     * 构造新的子对象
     * 新增ID字段，ParentID字段，通过Keys与父节点进行关联
     */
    private initNewObject(parentObject: GSPViewObject): GSPViewObject {
        const newObject = InitBizObject.initBizObject();

        // 新增父节点id字段
        const parentIDElement = this.initParentIDElement();
        newObject.ContainElements.push(parentIDElement);

        // 子节点新增与父节点的关联
        const key = new GSPAssociationKey();
        const idElement = this.getIDElement(parentObject);
        key.TargetElement = idElement.ID;
        key.TargetElementDisplay = idElement.Name;
        key.SourceElement = parentIDElement.ID;
        key.SourceElementDisplay = parentIDElement.Name;
        if (isNullOrUndefined(newObject.Keys)) {
            newObject.Keys = new Array<GSPAssociationKey>();
        }
        newObject.Keys.push(key);
        return newObject;
    }

    /**
     * 当前节点主键字段
     */
    getIDElement(bizObject: GSPViewObject): GSPViewModelElement {
        if (bizObject.ColumnGenerateID != null) {
            return bizObject.ContainElements.find(ele => ele.ID === bizObject.ColumnGenerateID.ElementID) as GSPViewModelElement;
        }
        return null;
    }

    /**
     * 子对象上的parentID字段初始化
     */
    private initParentIDElement(): GSPViewModelElement {
        const parentIDElement = new GSPViewModelElement();
        parentIDElement.ID = Guid.newGuid().toString();
        parentIDElement.Code = 'ParentID';
        parentIDElement.Name = 'ParentID';
        parentIDElement.LabelID = 'ParentID';
        parentIDElement.MDataType = GSPElementDataType.String;
        parentIDElement.ObjectType = GSPElementObjectType.None;
        parentIDElement.Length = 0;
        parentIDElement.Precision = 0;
        return parentIDElement;
    }

    private isMap(value: any): boolean {
        return Map.isMap(value);
    }

    private isList(value: any): boolean {
        return List.isList(value);
    }

    private isDbeObject(obj: GSPViewObject): boolean {
        // return obj.IsRef;
        return false;
    }

    /**
     * 判断当前路径为实体节点
     * 实体路径为[]
     * @param path
     */
    private isBENode(path: string[]): any {
        return path.length === 0;
    }

    /**
     * 当前选中节点是否为BE节点
     */
    isBeNodeSelect(): boolean {
        const currentObjectPath = this.convertObjectIndexToPath(this.currentSelectObjectIndex);
        return this.isBENode(currentObjectPath);
    }

    /**
     * 判断当前路径为主对象节点
     * 主对象路径为['MainObject']
     * @param path
     */
    private isMainObjectNode(path: string[]): any {
        return path.length === 1;
    }
}



