import { isNullOrUndefined } from 'util';
import { BizMgrAction } from '@gsp-bef/gsp-be-metadata';

export class InternalMgrActionUtil {
  /**
   * 内置操作的id列表，与后端一致
   */
  private static deleteMgrActionId = '9sEsnADPFkCRK6ZBC0OwiA';
  private static modifyMgrActionId = '8Wna6KaBQEOHpRJfmGMBYQ';
  private static queryMgrActionId = 'm_6xIwJwoUy7qzsuZ6_S5A';
  private static retrieveDefaultMgrActionId = 'Tji_dbrLtEKQlOLRpOJj9Q';
  private static retrieveMgrActionId = 'fQbpcO7bIEGoRdTZZIyrag';

  /**
   * 是否内置的删除操作
   * @param action
   */
  static isDeleteMgrAction(action: BizMgrAction): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.deleteMgrActionId;
  }

  /**
   * 是否内置的修改操作
   * @param action
   */
  static isModifyMgrAction(action: BizMgrAction): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.modifyMgrActionId;
  }

  /**
   * 是否内置的查询操作
   * @param action
   */
  static isQueryMgrAction(action: BizMgrAction): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.queryMgrActionId;
  }

  /**
   * 是否内置的新增操作
   * @param action
   */
  static isRetrieveDefaultMgrAction(action: BizMgrAction): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.retrieveDefaultMgrActionId;
  }

  /**
   * 是否内置的取数操作
   * @param action
   */
  static isRetrieveMgrAction(action: BizMgrAction): boolean {
    if (isNullOrUndefined(action)) {
      return false;
    }
    return action.ID === this.retrieveMgrActionId;
  }

  /**
   * 是否内置的MgrAction
   * @param action
   */
  static isInternalMgrAction(action: BizMgrAction): boolean {
    return this.isDeleteMgrAction(action) || this.isModifyMgrAction(action)
      || this.isQueryMgrAction(action) || this.isRetrieveDefaultMgrAction(action) || this.isRetrieveMgrAction(action);
  }
}



