import { OpRelationEntity } from './op-relatioin.entity';
import { AuthFieldEntryEntity } from './auth-field-entry.entity';
import { AuthorizationEntity } from '../../authorization';

/*权限访问场景实体*/
export class SecurityEntryEntity {
    id: string;
    ex1: string;
    ex2: string;
    ex3: string;
    ex4: string;
    ex5: string;
    extType: string;  /*扩展类型：比如VM，BE，此处防止不同扩展ext1、ext2等一样，导致查找失误*/
    authType: string; /*权限类型：0 数据权限，1：功能权限*/
    opRelations: Array<OpRelationEntity>;
    authFields: Array<AuthFieldEntryEntity>;
    authObjIds: string; // 权限对象以逗号隔开
    authObjNames: string; // 权限对象以逗号隔开
}





