/**
 * 参数输入输出模式
 */
export enum VMParameterMode {
  IN = 'IN',
  OUT = 'OUT',
  INOUT = 'INOUT'
}

export const VMParameterModes: Array<{ text: string, value: VMParameterMode }> = [
  { text: 'IN', value: VMParameterMode.IN },
  { text: 'OUT', value: VMParameterMode.OUT },
  { text: 'INOUT', value: VMParameterMode.INOUT }
];



