
import { Pipe, PipeTransform } from '@angular/core';
import { GSPAssociation, GSPEnumValue, GSPElementObjectTypes, GSPElementDataTypes } from './vm-reference';
import { GSPViewModelElement, GSPElementDataType } from './libs';
import { isNullOrUndefined } from 'util';

/**
 * 字段对象类型
 */
@Pipe({ name: 'gspElementObjectTypeConvert' })
export class GSPElementObjectTypeConvert implements PipeTransform {
  transform(objectType: any): string {
    if (!objectType) { alert('objectType为空'); }
    return GSPElementObjectTypes.find(item => item.value === objectType).text;
  }
}

/**
 * 字段数据类型
 */
@Pipe({ name: 'gspElementDataTypeConvert' })
export class GSPElementDataTypeConvert implements PipeTransform {
  transform(ele: GSPViewModelElement): string {
    if (!ele.IsVirtualViewElement && ele.IsUdt) {
      // 兼容之前有的业务字段的名称未赋值，显示'业务字段'
      if (!isNullOrUndefined(ele.UdtName) && ele.UdtName.length > 0) {
        return ele.UdtName;
      }
      return '业务字段';
    }
    const dataType = ele.MDataType;
    if (!dataType) { alert('dataType为空'); }
    return GSPElementDataTypes.find(item => item.value === dataType).text;
  }
}

/**
 * 关联列表
 */
@Pipe({ name: 'gspAssociationModelNames' })
export class GSPAssociationModelNames implements PipeTransform {
  transform(childAssociations: GSPAssociation[]) {
    if (childAssociations == null || childAssociations.length === 0) {
      return;
    }
    let models = '';
    childAssociations.forEach(ass => {
      models += ass.RefModelName + ',';
    });
    return models.slice(0, models.length - 1);
  }
}

/**
 * 枚举列表
 */
@Pipe({ name: 'gspEnumValues' })
export class GSPEnumValues implements PipeTransform {
  transform(enumList: GSPEnumValue[]) {
    if (enumList == null || enumList.length === 0) {
      return;
    }
    let enums = '';
    enumList.forEach(enumItem => {
      enums += enumItem.Value + ':' + enumItem.Name + ',';
    });
    return enums.slice(0, enums.length - 1);
  }
}


export const GSPViewModelElementDataTypes:
  Array<{
    text: string, value: GSPElementDataType | string,
    canChangeLength: boolean, canChangePresicion: boolean, length: number, precision: number
  }> = [
    {
      text: '文本', value: GSPElementDataType.String, canChangeLength: true,
      canChangePresicion: false, length: 36, precision: 0
    },
    {
      text: '备注', value: GSPElementDataType.Text, canChangeLength: false,
      canChangePresicion: false, length: 0, precision: 0
    },
    {
      text: '整数', value: GSPElementDataType.Integer, canChangeLength: false,
      canChangePresicion: false, length: 0, precision: 0
    },
    {
      text: '浮点数字', value: GSPElementDataType.Decimal, canChangeLength: true,
      canChangePresicion: true, length: 18, precision: 0
    },
    {
      text: '布尔型', value: GSPElementDataType.Boolean, canChangeLength: false,
      canChangePresicion: false, length: 1, precision: 0
    },
    {
      text: '日期', value: GSPElementDataType.Date, canChangeLength: false,
      canChangePresicion: false, length: 0, precision: 0
    },
    {
      text: '时间', value: GSPElementDataType.DateTime, canChangeLength: false,
      canChangePresicion: false, length: 0, precision: 0
    },
    {
      text: '二进制', value: GSPElementDataType.Binary, canChangeLength: false,
      canChangePresicion: false, length: 0, precision: 0
    },
  ];





