import { ViewChild, Component, OnInit, Output, EventEmitter, Input, Injectable, SimpleChanges, OnChanges } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { of } from 'rxjs/observable/of';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

import {
  GSPViewModel, GSPBizEntityObject, ViewModelConst, ViewObjectList, ViewModel, ViewObject, GSPViewObject
} from '../util/vm-reference';
import { ObjectTreeService } from './objectTree.service';
import { SelectBeObjectComponent } from './select-be-object/select-be-object.component';

@Component({
  selector: 'app-vm-object-tree',
  templateUrl: './object-tree.component.html',
  providers: [ObjectTreeService],
  styleUrls: ['./object-tree.component.css'],
})
export class ObjectTreeComponent implements OnInit, OnChanges {

  @ViewChild('selectBeObj') selectBeObj: SelectBeObjectComponent;

  @Input() viewModel: ViewModel;
  @Output() changeCurrentObject: EventEmitter<ViewObject> = new EventEmitter<ViewObject>();
  @Output() currentSelectItem: EventEmitter<ViewObject | ViewModel> = new EventEmitter<ViewObject | ViewModel>();

  /**
   * 对象树数据源
   */
  bizObjects: any[];
  expandedKeys: string[] = [];
  selectedKeys: string[] = [];


  /**
   * 当前选中节点是否为BE节点
   */
  isBeNodeSelect(): boolean {
    return this.objTreeService.isBeNodeSelect();
  }

  constructor(private objTreeService: ObjectTreeService) {

  }

  ngOnInit(): void {
    this.load(this.viewModel);
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 此处修改源于异步加载实体数据，直到加载到实体结构，才订阅选中对象改变事件。
    // this.refresh(this.viewModel);
    const change = changes['viewModel'];
    if (isNullOrUndefined(change)) {
      return;
    }

    // ① 传入空数据
    if (isNullOrUndefined(change.currentValue)) {
      this.clearData();
    } else {
      // ② 上一次空，当前非空，初次加载
      if (isNullOrUndefined(change.previousValue)) {
        this.load(this.viewModel);
      } else {
        // ③ 上一次非空，当前非空，刷新
        this.refresh(this.viewModel);
      }
    }
  }

  /**
   * 清空数据
   */
  clearData(): any {
    this.bizObjects = [];
  }

  /**
   * 加载
   */
  public load(vm: ViewModel) {
    if (isNullOrUndefined(vm)) {
      return;
    }

    this.objTreeService.resetViewModel(vm);

    const bizEntity = this.viewModel.toJS() as GSPViewModel;

    this.bizObjects = [bizEntity]; // 更新树的数据源

    this.expandedKeys = ['0', '0_0'];
    // this.expandedKeys = [bizEntity.ID, bizEntity.MainObject.ID];
    this.selectedKeys = [bizEntity.MainObject.ID];

    this.objTreeService.changeSelectObject.subscribe(item => {
      if (isNullOrUndefined(this.objTreeService.currentObject)) {
        return;
      }
      this.selectedKeys = [this.objTreeService.currentObject.ID];
      this.changeCurrentObject.next(this.objTreeService.currentObject);
    });
    this.objTreeService.refreshView.subscribe(item => {
      this.refresh(item);
    });
    this.objTreeService.currentSelectItem.subscribe(item => {
      this.currentSelectItem.next(item);
    });
  }

  /**
   * 刷新数据源
   */
  public refresh(vm: ViewModel) {
    if (isNullOrUndefined(vm)) {
      return;
    }

    const expandedStore = this.expandedKeys;
    const selectStore = this.selectedKeys;

    this.objTreeService.resetViewModel(vm);
    this.bizObjects = [this.objTreeService.toJS()]; // 更新树的数据源

    // 若所选节点已不存在，应当选中主节点
    // if (this.objTreeService.currentObject == null) {
    //   this.handleSelection('0_0');
    // } else {
    //   this.handleSelection(this.objTreeService.currentSelectObjectIndex);
    // }
    // this.handleSelection(this.objTreeService.currentSelectObjectIndex);
    if (this.objTreeService.currentObject == null) {
      this.handleSelection('0_0');
    } else {
      this.objTreeService.selectObjectByIndex(this.objTreeService.currentSelectObjectIndex);
      this.selectedKeys = selectStore;
      this.expandedKeys = expandedStore;
    }
  }

  /**
   * 加载子节点
   * @param node
   */
  public fetchChildren(node: any): Observable<any[]> {
    // 判断当前node为BE节点还是对象节点
    if (node.MainObject) {
      return of([node.MainObject]); // 若为BE节点，则MainObject属性不为空，则返回主节点；
    } else {
      return of(node.ContainChildObjects); // 若为对象节点，则返回子节点集合；
    }
  }

  /**
   * 判断是否包含子节点
   * @param node
   */
  public hasChildren(node: any): boolean {
    // 判断当前为BE节点还是对象节点
    if (node.MainObject) {
      return true;
    } else {
      return node.ContainChildObjects && node.ContainChildObjects.length > 0;
    }
  }

  /**
   * 捕捉选择项改变事件,获取当前选中节点的索引
   * @param param0
   */
  public handleSelection({ index }: any): void {
    // if (index !== this.objTreeService.currentSelectObjectIndex) {
    this.objTreeService.selectObjectByIndex(index);
    // }
  }

  /**
   * 新增实体映射对象
   */
  addBeObject(): any {
    this.selectBeObj.load(this.viewModel.toJS());
  }

  /**
   * 新增后的主节点
   */
  selectNewBizObjects(newMainObj: GSPViewObject) {
    this.viewModel.mainObject = new ViewObject(newMainObj);
    this.refresh(this.viewModel);
  }

  /**
   * 新增子对象
   */
  addChildVirtualObject(): any {
    this.objTreeService.addChildObject();

    // 根据当前新增的节点展开当前对象
    this.expandObjectTree();
  }

  /**
   * 根据新增节点位置，展开节点至新增节点
   */
  expandObjectTree(): any {
    let index = this.objTreeService.currentSelectObjectIndex;
    while (index.length > 2) {
      if (!this.expandedKeys.includes(index)) {
        this.expandedKeys.push(index);
      }
      index = index.slice(0, index.length - 2);
    }
    if (!this.expandedKeys.includes(index)) {
      this.expandedKeys.push(index);
    }
  }

  /**
   * 新增同级对象
   */
  addSiblingVirtualObject(): any {
    this.objTreeService.addSiblingObject();

    // 根据当前新增的节点展开当前对象
    this.expandObjectTree();
  }

  /**
   * 删除选中对象
   */
  deleteObject(): any {
    this.objTreeService.deleteObject();
  }
}







