
import { AuthEntryEntity } from './authentryentity';
/*权限对象*/
export class AuthorizationEntity {
    id: string;
    code: string;
    name: string;
    parentid: string;
    type: string; /*类型：0 目录catalogue，1：普通权限对象*/
    isDetail: boolean;
    sortOrder: Int32Array;
    children: AuthorizationEntity[]; /*关联的操作*/
    authEntry: AuthEntryEntity[]; /*关联的权限字段*/
}



