/**
 * 构件方法参数类型
 */
export enum VMParameterType {
    /**
     * 字符型
     */
    String = 'String',
    /**
     * 布尔型
     */
    Boolean = 'Boolean',
    /**
     * 整数
     */
    Int32 = 'Int32',
    /**
     * 浮点数
     */
    Decimal = 'Decimal',
    /**
     * 双浮点
     */
    Double = 'Double',
    /**
     * 时间
     */
    DateTime = 'DateTime',
    /**
     * 对象类型
     */
    Object = 'Object',
    /**
     * 自定义
     */
    Custom = 'Custom',
}

export const VMParameterTypes: Array<{ index: number, text: string, value: VMParameterType, assembly: string, className: string }> = [
    { index: 0, text: '字符串', value: VMParameterType.String, assembly: 'mscorlib.dll', className: 'System.String' },
    { index: 1, text: '布尔型', value: VMParameterType.Boolean, assembly: 'mscorlib.dll', className: 'System.Boolean' },
    { index: 2, text: '整数', value: VMParameterType.Int32, assembly: 'mscorlib.dll', className: 'System.Int32' },
    { index: 3, text: '浮点数', value: VMParameterType.Decimal, assembly: 'mscorlib.dll', className: 'System.Single' },
    { index: 4, text: '双浮点数', value: VMParameterType.Double, assembly: 'mscorlib.dll', className: 'System.Double' },
    { index: 5, text: '时间类型', value: VMParameterType.DateTime, assembly: 'mscorlib.dll', className: 'System.DateTime' },
    { index: 6, text: '对象类型', value: VMParameterType.Object, assembly: 'mscorlib.dll', className: 'System.Object' },
    { index: 7, text: '自定义类型', value: VMParameterType.Custom, assembly: '', className: '' },
];



