import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {
    HttpUrlConst, GSPBizEntityObject, GSPBizEntityElement, BizMgrAction, GSPViewModelElement,
    GSPViewObject, MappedBizAction, GSPViewModel, ViewModelMapping
} from '../util/vm-reference';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';
import { CacheService } from '@farris/ide-devkit/services';
import { Observable } from 'rxjs';

@Injectable()
export class VmMappingService {

    private currentUrl = HttpUrlConst.currentUrl;

    constructor(private http: HttpClient, private cache: CacheService) {
    }
    private sessionId = this.cache.get('sessionId');
    private vmBaseUrl = `${this.currentUrl}`
        + `/api/dev/main/v1.0/viewmodel`;
    private getRefBeObjUrl = `${this.vmBaseUrl}/getbizObject/`;
    private convertBizElementsUrl = `${this.vmBaseUrl}/convertElements`;
    private convertActionsUrl = `${this.vmBaseUrl}/convertActions`;
    private convertObjectsUrl = `${this.vmBaseUrl}/convertObjects`;
    private addObjectsUrl = `${this.vmBaseUrl}/addObjects`;
    // private convertBizEntityUrl = `${this.vmBaseUrl}/convertBizEntity`;

    /**
     * 映射对象
     */
    getRefObjForAddElements = new Subject<GSPBizEntityObject>();
    getRefObjForEditElement = new Subject<GSPBizEntityObject>();

    /**
     * 新增的vm字段
     */
    addNewVmElements = new Subject<GSPViewModelElement[]>();

    /**
     * 新增的vm对象
     */
    vmAfteraddObjects = new Subject<GSPViewModel>();

    /**
     * 新增的vm操作
     */
    addNewVmActions = new Subject<MappedBizAction[]>();

    private getHeadersWithSessionId(): any {
        const headers = new HttpHeaders();
        headers.append('sessionId', this.sessionId);
    }

    /**
     * 根据beid及bizObjId读取bizObj
     */
    getRefBizObjectForAddElements(bePkgName: string, beMetadataID: string, bizObjID: string) {
        this.getRefObjctByID(bePkgName, beMetadataID, bizObjID).subscribe(data => {
            this.getRefObjForAddElements.next(data as GSPBizEntityObject);
        }, () => alert('获取当前对象对应的业务实体对象失败。'));
    }

    getRefBizObjectForEditElement(bePkgName: string, beMetadataID: string, bizObjID: string) {
        this.getRefObjctByID(bePkgName, beMetadataID, bizObjID).subscribe(data => {
            this.getRefObjForEditElement.next(data as GSPBizEntityObject);
        }, () => alert('获取当前对象对应的业务实体对象失败。'));
    }
    /**
     * 根据beid及bizObjId读取bizObj
     */
    getRefObjctByID(bePkgName: string, beMetadataID: string, bizObjID: string): Observable<any> {
        if (isNullOrUndefined(beMetadataID) || beMetadataID.length === 0) {
            alert('业务实体元数据ID为空。');
            return;
        }
        if (isNullOrUndefined(bizObjID) || bizObjID.length === 0) {
            alert('当前对象对应的业务实体对象ID为空。');
            return;
        }
        const path = HttpUrlConst.currentPath;
        const url = this.getRefBeObjUrl + `${beMetadataID}/${bizObjID}?path=${path}&&bePkgName=${bePkgName}`;

        const options = {
            params: {},
            headers: this.getHeadersWithSessionId()
        };
        return this.http.get(url, options);
    }
    /**
     * 批量新增字段
     */
    convertBizElementsToViewElements(elements: GSPBizEntityElement[], mapping: ViewModelMapping) {
        const info = new ConvertEleInfo();
        info.bizElementsJson = JSON.stringify(elements);
        info.bePkgName = mapping.TargetMetadataPkgName;
        info.beId = mapping.TargetMetadataId;
        const options = {
            params: {},
            headers: this.getHeadersWithSessionId()
        };
        this.http.put(this.convertBizElementsUrl, info, options).subscribe(vmElements => {
            this.addNewVmElements.next(vmElements as GSPViewModelElement[]);
        });
    }

    /**
     * 批量新增对象
     */
    addObjects(bizObjectIDs: string[], vm: GSPViewModel) {
        const info = new AddObjsInfo();
        info.vmJson = JSON.stringify(vm);
        info.bizObjIds = JSON.stringify(bizObjectIDs);
        info.path = HttpUrlConst.currentPath;
        info.bePkgName = vm.Mapping.TargetMetadataPkgName;
        info.beId = vm.Mapping.TargetMetadataId;
        const options = {
            params: {},
            headers: this.getHeadersWithSessionId()
        };
        this.http.put(this.addObjectsUrl, info, options).subscribe(newVm => {
            this.vmAfteraddObjects.next(newVm as GSPViewModel);
        });
    }

    // /**
    //  * 批量新增对象
    //  */
    // convertBizObjectsToViewObjects(objects: GSPBizEntityObject[]) {
    //     const info = new ConvertObjInfo();
    //     info.bizObjectsJson = JSON.stringify(objects);
    //     info.bePkgName = HttpUrlConst.currentVmMapping.TargetMetadataPkgName;
    //     info.beId = HttpUrlConst.currentVmMapping.TargetMetadataId;
    //     const options = {
    //         params: {},
    //         headers: this.getHeadersWithSessionId()
    //     };
    //     this.http.put(this.convertObjectsUrl, info, options).subscribe(vmObjects => {
    //         // this.addNewVmObjects.next(vmObjects as GSPViewObject[]);
    //     });
    // }

    /**
     * 批量新增操作
     */
    convertBizMgrActionsToViewActions(actions: BizMgrAction[], mapping: ViewModelMapping) {
        const info = new ConvertActionInfo();
        info.bizActionsJson = JSON.stringify(actions);
        info.bePkgName = mapping.TargetMetadataPkgName;
        info.beId = mapping.TargetMetadataId;
        const options = {
            params: {},
            headers: this.getHeadersWithSessionId()
        };
        this.http.put(this.convertActionsUrl, info, options).subscribe(vmActions => {
            this.addNewVmActions.next(vmActions as MappedBizAction[]);
        });
    }

}

abstract class ConvertInfoBase {
    bePkgName: string;
    beId: string;
}

class ConvertEleInfo extends ConvertInfoBase {
    bizElementsJson: string;
}

class ConvertObjInfo extends ConvertInfoBase {
    bizObjectsJson: string;
}

class AddObjsInfo extends ConvertInfoBase {
    vmJson: string;
    bizObjIds: string;
    path: string;
}

class ConvertActionInfo extends ConvertInfoBase {
    bizActionsJson: string;
}



