import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { MapBase } from '../base/mapBase';
import { ViewElementList } from '../viewElement/viewElementList';
import { ListBase } from '../base/listBase';

import {
    GSPViewObject, GSPCommonObjectType, ViewObjectConst
} from '../../ref';
import { ViewMapping } from '../viewMapping/viewMapping';

export class ViewObject extends MapBase {

    /**
     * 业务对象名刷新事件
     */
    refreshName: Subject<boolean> = new Subject<boolean>();

    /**
     * 构造函数
     * @param object
     */
    constructor(object: GSPViewObject | Map<string, any>) {
        super(object);
        if (isNullOrUndefined(object)) {
            return;
        }
        this.data = this.getCurrentViewObject(this.data);
    }

    /**
     * 获取对象的Map
     * @param bizObject
     */
    private getCurrentViewObject(bizObject: Map<string, any>): Map<string, any> {
        if (isNullOrUndefined(bizObject)) {
            return;
        }
        let vo = bizObject;
        vo = vo.update(ViewObjectConst.ContainChildObjects, bizObjects => this.getChildrenViewObjects(bizObjects));
        vo = vo.update(ViewObjectConst.ContainElements, elements => this.getElementViewList(elements));
        vo = vo.update(
            ViewObjectConst.Mapping, mapping => this.getViewMapping(mapping as Map<string, any>)
        );
        return vo;
    }

    private getViewMapping(mapping: Map<string, any>) {
        if (isNullOrUndefined(mapping)) {
            return;
        }
        const viewValueHelpConfigs = new ViewMapping(mapping);
        return viewValueHelpConfigs;
    }

    private getElementViewList(bizElements: List<any>): ViewElementList {
        const elementList = new ViewElementList(bizElements);
        return elementList;
    }

    private getChildrenViewObjects(objectArray: List<any>): ViewObjectList {
        const objects = new ViewObjectList(objectArray);
        return objects;
    }

    get objectType(): GSPCommonObjectType {
        return this.get(ViewObjectConst.ObjectType);
    }

    get elements(): ViewElementList {
        return this.get(ViewObjectConst.ContainElements);
    }

    set elements(value: ViewElementList) {
        this.set(ViewObjectConst.ContainElements, value);
    }

    get mapping(): ViewMapping {
        return this.get(ViewObjectConst.Mapping);
    }

    set mapping(value: ViewMapping) {
        this.set(ViewObjectConst.Mapping, value);
    }

    get childrenObjects(): ViewObjectList {
        return this.get(ViewObjectConst.ContainChildObjects);
    }

    get ID() {
        return this.get(ViewObjectConst.ID);
    }

    get ParentObjectID() {
        return this.get(ViewObjectConst.ParentObjectID);
    }


    get Code(): string {
        return this.get(ViewObjectConst.Code);
    }

    set Code(code: string) {
        this.set(ViewObjectConst.Code, code);
    }

    get Name(): string {
        return this.get(ViewObjectConst.Name);
    }

    set Name(name: string) {
        this.set(ViewObjectConst.Name, name);
        this.refreshName.next(true);
    }

    addChildObject(childObject: ViewObject) {
        if (isNullOrUndefined(this.childrenObjects)) {
            const childrenList = this.getChildrenViewObjects(List());
            childrenList.add(childObject);
            this.set(ViewObjectConst.ContainChildObjects, childrenList);
            return;
        } else {
            this.childrenObjects.add(childObject);
            this.refreshViewLayer();
        }
    }
}



export class ViewObjectList extends ListBase<ViewObject> {

    constructor(objectArray: GSPViewObject[] | List<any>) {
        super(objectArray);
        if (isNullOrUndefined(objectArray)) {
            return;
        }
        this.data = this.getChildrenViewObjects(this.data);
    }

    /**
     * 获取当前子对象列表的List
     * @param objectArray
     */
    private getChildrenViewObjects(objectArray: List<any>): List<any> {
        if (isNullOrUndefined(objectArray)) {
            return;
        }

        let viewObjectList = objectArray;

        if (objectArray.size > 0) {
            for (let i = 0; i < objectArray.size; i++) {
                viewObjectList = viewObjectList.update(i, () => this.getViewObject(objectArray.get(i) as Map<string, any>));

            }
            return viewObjectList;
        }
    }

    private getViewObject(bizObject: Map<string, any>): ViewObject {
        return new ViewObject(bizObject);
    }

}






