import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { GridModule } from '@progress/kendo-angular-grid';
import { LayoutModule } from '@progress/kendo-angular-layout';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ButtonsModule } from '@progress/kendo-angular-buttons';

import { SecurityEntryService } from './service/security-entry.service';

import { FuncOpSelectComponent } from './security-entry-func-op/func-op-select/func-op-select.component';
import { SecurityEntryFuncOpComponent } from './security-entry-func-op/security-entry-func-op.component';
import { SecurityEntryAuthOpComponent } from './security-entry-auth-op/security-entry-auth-op.component';
import { AuthFieldGridComponent } from './security-entry-auth-op/auth-field-grid/auth-field-grid.component';
import { AuthFieldSelectComponent } from './security-entry-auth-op/auth-field-grid/auth-field-select/auth-field-select.component';

import { AuthOpGridComponent } from './security-entry-auth-op/auth-op-grid/auth-op-grid.component';
import { AuthOpSelectComponent } from './security-entry-auth-op/auth-op-grid/auth-op-select/auth-op-select.component';

import { AuthorizationModule } from '../authorization/authorization.module';
// import { AuthfieldSelectComponent } from '../authorization/authfield/authfield-select/authfield-select.component';
// import { AuthfieldTreeComponent } from '../authorization/authfield/authfield-tree/authfield-tree.component';
// import { AuthfieldComponent } from '../authorization/authfield/authfield.component';

@NgModule({
  imports: [
    CommonModule,
    ButtonsModule,
    GridModule,
    TreeViewModule,
    AuthorizationModule
  ],
  entryComponents: [SecurityEntryFuncOpComponent, SecurityEntryAuthOpComponent, FuncOpSelectComponent, AuthFieldGridComponent
    , AuthFieldSelectComponent, AuthOpGridComponent, AuthOpSelectComponent],
  declarations: [
    SecurityEntryFuncOpComponent, SecurityEntryAuthOpComponent, FuncOpSelectComponent, AuthFieldGridComponent
    , AuthFieldSelectComponent, AuthOpGridComponent, AuthOpSelectComponent],
  exports: [SecurityEntryFuncOpComponent, SecurityEntryAuthOpComponent, FuncOpSelectComponent, AuthFieldGridComponent
    , AuthFieldSelectComponent, AuthOpGridComponent, AuthOpSelectComponent],
  providers: [SecurityEntryService]
})

export class SecurityEntryModule { }




