import {
  ComponentRef, Component, ViewChild, Input, OnChanges, Output, EventEmitter,
  SimpleChanges, ComponentFactoryResolver, Injector, TemplateRef
} from '@angular/core';
import { BsModalService } from '@farris/ui';
import { GSPBizEntityObject, GSPBusinessEntity, GSPElementObjectType, ViewElement, GSPViewModel } from '../../../util/vm-reference';
import { ObjectTypeEditorComponent } from '../../../element-collection/object-type-editor/object-type-editor.component';

@Component({
  selector: 'app-object-type-editor-in-vm',
  templateUrl: './object-type-editor-in-vm.component.html',
  styleUrls: ['./object-type-editor-in-vm.component.css']
})
export class ObjectTypeEditorInVmComponent {

  isModalShown = false;

  @Input() viewModel: GSPViewModel;
  @Input() currentObject: GSPBizEntityObject;
  @Input() currentViewElement: ViewElement;
  @Output() changeObjType: EventEmitter<GSPElementObjectType> = new EventEmitter<GSPElementObjectType>();
  @ViewChild('buttons') btnRef: TemplateRef<any>;
  canEdit: boolean;
  compRef: ComponentRef<ObjectTypeEditorComponent>;
  constructor(private modalService: BsModalService, private resolver: ComponentFactoryResolver, private injector: Injector) {
  }

  openMetadataSelect() {
    const compFactory = this.resolver.resolveComponentFactory(ObjectTypeEditorComponent);
    const compRef = compFactory.create(this.injector);
    this.compRef = compRef;
    compRef.instance.viewModel = this.viewModel;
    compRef.instance.currentObject = this.currentObject;
    compRef.instance.viewElement = this.currentViewElement;
    compRef.instance.canEdit = this.canEdit;
    compRef.instance.changeObjType.subscribe($event => {
      console.log('$event', $event);
      this.changeObjType.next($event);
    });
    const modalConfig = {
      title: '对象类型编辑器',
      width: 710,
      height: 610,
      showButtons: true,
      buttons: this.btnRef
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe(() => {
      dialog.close();
    });
  }

  onCancel() {
    this.compRef.instance.onCancel();
  }

  onConfirm() {
    this.compRef.instance.onConfirm();
  }
  /**
   * 显示对象类型编辑框
   */
  load(currentViewElement: ViewElement, canEdit: boolean): any {
    this.currentViewElement = currentViewElement;
    this.canEdit = canEdit;
    this.openMetadataSelect();
  }
}



