import {
  Component, ViewChild, Input, OnChanges, Output, EventEmitter, SimpleChanges, ComponentFactoryResolver, Injector
} from '@angular/core';
import { isNullOrUndefined } from 'util';
// tslint:disable-next-line:max-line-length
import { MetadatapackageselectComponent } from '@gsp-lcm/metadata-selector/metadata/metadata-select/components/metadatapackageselect/metadatapackageselect.component';
import { Metadata4RefDto, SelectMetadataComponent } from '@gsp-lcm/metadata-selector';
import { ModalDirective } from '@farris/ui/container/modal/modal/modal.directive';
import { BsModalService } from '@farris/ui';
import { HttpUrlConst } from '../../../util/vm-reference';

@Component({
  selector: 'app-select-metadata-in-vm',
  templateUrl: './selectmetadata.component.html',
  styleUrls: ['./selectmetadata.component.css']
})
export class SelectMetadataInVmComponent implements OnChanges {
  @ViewChild('autoShownModal') autoShownModal: ModalDirective;
  @ViewChild(MetadatapackageselectComponent) metadatapackageselectComponent: MetadatapackageselectComponent;
  classifier: string;
  isModalShown = false;
  isExcuteCancel = true;
  @Input() triggerShowWindow: boolean;
  @Input() typeName: string;

  @Output() currentBeMetadata: EventEmitter<any> = new EventEmitter<any>();
  @Output() closeWindow: EventEmitter<boolean> = new EventEmitter<boolean>();

  gspmetadata: Metadata4RefDto;

  constructor(private modalService: BsModalService,
    private resolver: ComponentFactoryResolver, private injector: Injector) {
  }

  ngOnChanges(changes: SimpleChanges): void {
    // 初次加载不显示
    if (isNullOrUndefined(changes.triggerShowWindow.previousValue)) {
      return;
    }
    if (!this.isModalShown) {
      this.openMetadataSelect();
    }
  }

  openMetadataSelect() {
    const compFactory = this.resolver.resolveComponentFactory(SelectMetadataComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.metadataPath = HttpUrlConst.currentPath;
    compRef.instance.typeName = this.typeName;
    compRef.instance.selectedMetadata.subscribe($event => {
      console.log('$event', $event);
      dialog.close();
      this.currentBeMetadata.next($event);
    });
    const modalConfig = {
      title: '元数据选择',
      width: 882,
      height: 573,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe(() => {
      dialog.close();
    });
  }
}



