/**
 * VO操作类型枚举
 * @author wangjiegj<wangjiegj@inspur.com>
 */

/**
 * VO操作类型枚举
 */
enum ViewModelActionType {
    /**
     * BE的业务操作
     */
    BEAction = 'BEAction',
    /**
     * VM 操作
     */
    VMAction = 'VMAction',

    /**
     * 自定义操作
     */
    Custom = 'Custom'

}

export { ViewModelActionType };

export const ViewModelActionTypes: Array<{ text: string, value: ViewModelActionType }> = [
    { text: 'BE操作', value: ViewModelActionType.BEAction },
    { text: 'VM操作', value: ViewModelActionType.VMAction },
    // { text: 'INOUT', value: ViewModelActionType.Custom } // 暂不支持
  ];




