import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { MapBase } from '../base/mapBase';
import { ViewObject } from '../viewObject/viewObject';
import { ViewAssociationList } from '../viewAssociation/viewAssociationList';
import { ViewValueHelpConfigList } from '../viewValueHelpConfig//viewValueHelpConfig';

import { GSPViewModel, BECategory, ViewModelConst } from '../../ref';
import { ViewActionList } from '../viewOperation/viewActionBaseList';
import { ViewMapping } from '../viewMapping/viewMapping';
import { ViewModelMapping } from '../../../das';

export class ViewModel extends MapBase {

    /**
     * 视图对象名刷新事件
     */
    refreshName: Subject<boolean> = new Subject<boolean>();

    /**
     * 构造函数
     * @param vm
     */
    constructor(vm: GSPViewModel) {
        super(vm);
        this.data = this.initViewModel(this.data);
    }

    /**
     * 构造实体的Map结构
     * @param map
     */
    private initViewModel(map: Map<string, any>) {
        if (isNullOrUndefined(map)) {
            return;
        }
        const vm = map.update(
            ViewModelConst.MainObject, mainObject => this.getViewObject(mainObject as Map<string, any>)
        ).update(
            ViewModelConst.Actions, vmActions => this.getViewVMActions(vmActions as List<any>)
        ).update(
            ViewModelConst.ValueHelpConfigs, helpconfigs => this.getViewValueHelpConfigs(helpconfigs as List<any>)
        ).update(
            ViewModelConst.Mapping, mapping => this.getViewMapping(mapping as Map<string, any>)
        );
        return vm;
    }

    /**
     * 获取对象的Map
     * @param bizObject
     */
    private getViewObject(bizObject: Map<string, any>): ViewObject {
        if (isNullOrUndefined(bizObject)) {
            return;
        }
        const vo = new ViewObject(bizObject);
        return vo;
    }

    private getViewVMActions(mgrBizActions: List<any>) {
        if (isNullOrUndefined(mgrBizActions)) {
            return;
        }
        const viewMgrActions = new ViewActionList(mgrBizActions);
        return viewMgrActions;
    }

    private getViewValueHelpConfigs(valueHelpConfigs: List<any>) {
        if (isNullOrUndefined(valueHelpConfigs)) {
            return;
        }
        const viewValueHelpConfigs = new ViewValueHelpConfigList(valueHelpConfigs);
        return viewValueHelpConfigs;
    }

    private getViewMapping(mapping: Map<string, any>) {
        if (isNullOrUndefined(mapping)) {
            return;
        }
        const viewValueHelpConfigs = new ViewMapping(mapping);
        return viewValueHelpConfigs;
    }

    /**
     * 当前Action集合
     */
    get actions(): ViewActionList {
        const viewMgrActions = this.get(ViewModelConst.Actions);
        if (isNullOrUndefined(viewMgrActions)) {
            this.set(ViewModelConst.Actions, new ViewActionList(null));
        }
        return viewMgrActions;
    }

    /**
     * 帮助配置集合
     */
    get valueHelpConfigs(): ViewValueHelpConfigList {
        const authrizations = this.get(ViewModelConst.ValueHelpConfigs);
        if (isNullOrUndefined(authrizations)) {
            this.set(ViewModelConst.Actions, new ViewAssociationList(null));
        }
        return authrizations;
    }

    set valueHelpConfigs(auths: ViewValueHelpConfigList) {
        this.set(ViewModelConst.ValueHelpConfigs, auths);
    }

    get mainObject(): ViewObject {
        return this.get(ViewModelConst.MainObject);
    }

    set mainObject(mainObj: ViewObject) {
        this.set(ViewModelConst.MainObject, mainObj);
    }

    get ID(): string {
        return this.get(ViewModelConst.ID);
    }

    get Code(): string {
        return this.get(ViewModelConst.Code);
    }

    set Code(code: string) {
        this.set(ViewModelConst.Code, code);
    }

    get Name(): string {
        return this.get(ViewModelConst.Name);
    }

    set Name(name: string) {
        this.set(ViewModelConst.Name, name);
        this.refreshName.next(true);
    }

    get mapping(): ViewModelMapping {
        return this.get(ViewModelConst.Mapping);
    }

    set mapping(mapping: ViewModelMapping) {
        this.set(ViewModelConst.Mapping, mapping);
    }

    toJS(): GSPViewModel {
        return super.toJS() as GSPViewModel;
    }
}



