import { Map } from 'immutable';

import { MapBase } from '../base/mapBase';

import { GSPAssociationKey, ViewElementConst } from '../../ref';

export class ViewAssoKey extends MapBase {

    /**
     * 构造函数
     * @param element
     */
    constructor(asso: GSPAssociationKey | Map<string, any>) {
        super(asso);
    }

    updateSourceElement(id: string, name: string) {
        this.set(ViewElementConst.SourceElement, id);
        this.set(ViewElementConst.SourceElementDisplay, name);
    }

    updateTargetElement(id: string, name: string) {
        this.set(ViewElementConst.TargetElement, id);
        this.set(ViewElementConst.TargetElementDisplay, name);
    }
}



