import {
  Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter,
  SimpleChanges, Renderer2, Injector, ComponentFactoryResolver
} from '@angular/core';
import { FormArray, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

import { AddEvent, EditEvent, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';

import { ModalDirective } from '@farris/ui/container/modal/modal/modal.directive';
import { BsModalService } from '@farris/ui';

import { AuthFieldEntryEntity } from '../../entity/auth-field-entry.entity';
import { AuthOpRelationEntity } from '../../entity/auth-op-relation.entity';
import { SecurityEntryService } from '../../service/security-entry.service';
import { isNullOrUndefined } from 'util';

import { AuthoperationEntity } from '../../../authorization/entity/authoperationentity';
import { AuthOpSelectComponent } from './auth-op-select/auth-op-select.component';

@Component({
  selector: 'app-auth-op-grid',
  templateUrl: './auth-op-grid.component.html',
  styleUrls: ['./auth-op-grid.component.css']
})
export class AuthOpGridComponent implements OnInit, OnChanges {

  // 权限配置中权限字段关联的动作分配信息
  @Input() authFieldEntryEntity: AuthFieldEntryEntity;

  //  权限配置中要关联的动作
  @Input() actions: any[];
  // 要展示的数据操作所隶属的权限对象
  @Input() authorizationId: string;
  // 要展示的数据操作所关联的的权限字段
  @Input() authfield: string;


  public currentItem: any;
  public selectedKeys: any[] = [];
  // public selectableSettings: SelectableSettings = {
  //   checkboxOnly: false,
  //   mode: 'single'
  // };

  public formGroup: FormGroup;
  public formGroups: FormGroup = new FormGroup({ items: new FormArray([]) });
  private editedRowIndex: number;
  datas: AuthOpRelationEntity[] = [];

  constructor(
    private securityEntryervice: SecurityEntryService,
    private formBuilder: FormBuilder,
    private renderer: Renderer2,
    private modalService: BsModalService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector) { }
  // 功能操作相关
  operationSelected: AuthoperationEntity;

  ngOnInit() {
    this.loadData();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.loadData();
  }



  loadData() {
    if (this.authFieldEntryEntity) {
      if (isNullOrUndefined(this.authFieldEntryEntity.authOpRelations)) {
        this.authFieldEntryEntity.authOpRelations = [];
      }
      this.datas = this.authFieldEntryEntity.authOpRelations;
    } else {
      return;
    }

    if (this.actions) {
      this.actions.forEach(action => {
        const item = this.datas.find(opdata => opdata.actionId === action.id);
        if (item) {
          item.actionCode = action.code;
          item.actionName = action.name;
        } else {
          const newRelation = new AuthOpRelationEntity();
          newRelation.actionId = action.id;
          newRelation.actionName = action.name;
          this.datas.push(newRelation);
        }
      });
    }
  }

  /**
   * 开始编辑
   */
  public onCellClick(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, type } = args;
    // 若为新增行，不响应
    if (rowIndex === -1) {
      return;
    }
    this.currentItem = dataItem;
    if (column.field === 'operationName') {
      this.ShowOperationDialog();
    }
  }

  private createFormGroup(dataItem: any): FormGroup {
    return this.formBuilder.group({
      'id': [dataItem.id],
      'actionId': [dataItem.actionId],
      'operationId': [dataItem.operationId],
      'operationCode': [dataItem.operationCode],
      'operationName': [dataItem.operationName]
    });
  }

  /**
  * 编辑结束
  *
  */
  private onCellClose(args: any) {
    alert('cellClose');
    const { sender, formGroup, dataItem, rowIndex, column } = args;
    let item = this.datas.find(x => x.actionId === dataItem['actionId']);
    if (isNullOrUndefined(item)) {
      item = new AuthOpRelationEntity();
    }
    const index = this.datas.indexOf(item);
    const propertyName = column.field;
    const newValue = formGroup.value[propertyName];

    switch (column.field) {
      case 'operationId':
        item.operationId = newValue;
        break;
    }
  }


  /**
  * 加载功能操作
  *
  */
  ShowOperationDialog() {
    const compFactory = this.resolver.resolveComponentFactory(AuthOpSelectComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.authfield = this.authfield;
    compRef.instance.authorizationId = this.authorizationId;
    compRef.instance.selectOperationEmitter.subscribe(data => {
      dialog.close();
      this.operationSelected = data;
      this.currentItem.operationId = data.id;
      this.currentItem.operationName = data.name;
      this.currentItem.operationCode = data.code;
    });
    const modalConfig = {
      title: '操作选择',
      width: 400,
      height: 500,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe($event => {
      dialog.close();
    });
  }

  /**
   * 保存权限扩展信息
   *
   */
  save() {
  }
}




