import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';
import { Subscriber } from 'rxjs/Subscriber';
import { filter } from 'rxjs/operators';
import { AuthorizationEntity } from '../entity/authorizationentity';
import { isNullOrUndefined } from 'util';
import { HttpUrlConst } from '../../../../util/vm-reference';

import { Response, Headers, RequestOptions, URLSearchParams, Http } from '@angular/http';
import { CacheService } from '@farris/ide-devkit/services';

@Injectable(
  // {  providedIn:'root'}
)
export class AuthorizationService {
  // currentUrl = HttpUrlConst.currentUrl;
  constructor(private http: Http, private cache: CacheService) { }
  ip = HttpUrlConst.currentUrl;
  sessionId = this.cache.get('sessionId');

  GetAuthorizationTree(): Observable<any> {
    const url = this.ip + `/api/runtime/sys/v1.0/authorizations`;
    const headers = new Headers();
    headers.append('sessionId', this.sessionId);
    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return this.http.get(url, options).pipe(
      map((res: any) => res.json()));
    //  return of(testDataAuthorization);
  }


  GetAuthfields(authorizationIds: string[]): Observable<any> {
    const url = `${this.ip}` + `/api/runtime/sys/v1.0/authorizations/authfields?authIds=` + authorizationIds.toString();
    const headers = new Headers();
    headers.append('sessionId', this.sessionId);
    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return this.http.get(url, options).pipe(
      map((res: any) => res.json()));
  }

  GetAuthOperations(authorizationId: string, authfieldId: string): Observable<any> {
    const url = `${this.ip}` + `/api/runtime/sys/v1.0/authorizations/` + authorizationId + `/authOps?authField=` + authfieldId;
    const headers = new Headers();
    headers.append('sessionId', this.sessionId);
    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return this.http.get(url, options).pipe(
      map((res: any) => res.json()));
  }

  // 获取功能操作
  GetFuncoperations(): Observable<any> {
    const url = `${this.ip}` + `/api/runtime/sys/v1.0/FuncOperations`;
    const headers = new Headers();
    headers.append('sessionId', this.sessionId);
    const options = new RequestOptions({
      params: {},
      headers: headers
    });
    return this.http.get(url, options).pipe(
      map((res: any) => res.json()));
  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      return of(result as T);
    };
  }
}

