import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';

import { ValueHelpConfig } from '../../ref';

export class ViewValueHelpConfigList extends ListBase<ViewValueHelpConfig> {

  constructor(enumArray: ValueHelpConfig[] | List<any>) {
    super(enumArray);
    if (isNullOrUndefined(enumArray)) {
      return;
    }
    this.data = this.getViewValueHelpConfigs(this.data);
  }

  getViewValueHelpConfigs(authInfos: List<any>): any {
    if (isNullOrUndefined(authInfos)) {
      return;
    }

    let viewAuthInfos = authInfos;

    if (authInfos.size > 0) {
      for (let i = 0; i < authInfos.size; i++) {
        viewAuthInfos = viewAuthInfos.update(i, value => this.getViewAuthInfo(authInfos.get(i)));
      }
      return viewAuthInfos;
    }
  }

  getViewAuthInfo(authInfo: ValueHelpConfig): any {
    return new ViewValueHelpConfig(authInfo);
  }
}

export class ViewValueHelpConfig extends MapBase {

  /**
   * 构造函数
   * @param valueHelpConfig
   */
  constructor(valueHelpConfig: ValueHelpConfig | Map<string, any>) {
    super(valueHelpConfig);
  }
}




