import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';

import { ViewElement } from './viewElement';
import { ListBase } from '../base/listBase';

import { GSPBizEntityElement } from '../../ref';



export class ViewElementList extends ListBase<ViewElement> {

    constructor(elements: GSPBizEntityElement[] | List<any>) {
        super(elements);
        if (elements) {
            this.data = this.getElements(this.data);
        }
    }

    /**
     * 构造时获取当前对象的字段集合列表的List
     * @param elementArray
     */
    private getElements(elementArray: List<any>): List<any> {
        if (isNullOrUndefined(elementArray)) {
            return;
        }

        let viewElementList = elementArray;

        if (elementArray.size > 0) {
            for (let i = 0; i < elementArray.size; i++) {
                viewElementList = viewElementList.update(i, obj => this.getViewElement(elementArray.get(i)));
            }
            return viewElementList;
        }
    }

    /**
     * 构造时获取字段的Map
     * @param element
     */
    protected getViewElement(element: Map<string, any>): any {
        const viewEle = new ViewElement(element);
        return viewEle;
    }


    // /**
    //  * 当前字段枚举值
    //  */
    // get viewEnums() {
    //     let enums = this.currentSelectItem.get(bizElementConst.ContainEnumValues) as viewEnumValueList;
    //     enums.refreshView.subscribe(value => this.refreshViewLayer());
    //     return enums;
    // }

    // /**
    //  * 当前字段关联值
    //  */
    // get viewAssociations() {

    //     let associations = this.currentSelectItem.get(bizElementConst.ChildAssociations) as viewAssociationList;
    //     associations.refreshView.subscribe(
    //         value => {
    //             this.refreshViewLayer()
    //         }
    //     );
    //     return associations;
    // }
}



