import { Component, OnInit, EventEmitter, Output, ViewChild } from '@angular/core';
import { ObjectTreeComponent } from '../object-tree.component';
import { ObjectTreeService } from '../objectTree.service';
import { GSPBusinessEntity, GSPBizEntityObject, IGSPCommonObject } from '@gsp-bef/gsp-be-metadata';
import { GSPViewModel, MetadataDto, HttpUrlConst, GSPViewObject, BaseConst } from '../../util/vm-reference';
import { VmMetadataService } from '../../service/vm-metadata.service';
import { of } from 'rxjs/observable/of';
import { Observable } from 'rxjs/Observable';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { isNullOrUndefined } from 'util';
import { VmMappingService } from '../../service/vm-mapping.service';
import { TreeViewGroupComponent } from '@progress/kendo-angular-treeview/dist/es2015/treeview-group.component';

@Component({
  selector: 'app-select-be-object',
  templateUrl: './select-be-object.component.html',
  styleUrls: ['./select-be-object.component.css'],
  providers: [VmMetadataService, ObjectTreeService, VmMappingService],
})
export class SelectBeObjectComponent {

  @ViewChild('treeview') treeview: TreeViewGroupComponent;

  active = false;

  identifier = BaseConst.IDENTIFIER;


  @Output() cancel: EventEmitter<any> = new EventEmitter();
  @Output() save: EventEmitter<GSPViewObject> = new EventEmitter(); // 新增节点后的主对象

  private checkedState: CheckedState = 'checked';
  private indeterminateState: CheckedState = 'indeterminate';
  private uncheckedState: CheckedState = 'none';

  /**
   * 对象树数据源
   */
  bizObjects: GSPBizEntityObject[];
  expandedKeys: string[] = [];
  currentVmObjects: GSPViewObject[];
  vm: GSPViewModel;
  disabledKeys = [];
  // disabledKeys = ['fccb5f59-49c9-4d55-8bf9-e68110c30a9e'];
  /**
   * checkbox选中
   */
  public checkedKeys: string[] = [];
  public currentVmKeys: string[] = [];

  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: false,
      checkParents: false,
      enabled: true,
      mode: 'multiple'
    };
  }
  constructor(private objService: ObjectTreeService, private metadataService: VmMetadataService, private mappingService: VmMappingService) {
    this.mappingService.vmAfteraddObjects.subscribe(newVm => {
      this.save.next(newVm.MainObject);
    });
  }

  load(vm: GSPViewModel) {
    const mapping = vm.Mapping;
    this.metadataService.getMappingBeMetadata(mapping.TargetMetadataPkgName, mapping.TargetMetadataId).subscribe(beMetadata => {
      const beJson = (<MetadataDto>beMetadata).content;
      this.active = true;
      this.vm = vm;
      this.currentVmObjects = [vm.MainObject];
      const be = JSON.parse(beJson);
      this.bizObjects = [be.MainObject];
      this.expandAll(this.bizObjects);
      this.getVmObjectsMappingIDs(this.currentVmObjects);

      if (this.checkedKeys.length === this.expandedKeys.length) {
        alert('业务实体对象已全部添加。');
        this.closeForm();
      }
    });
  }

  private expandAll(objs: GSPBizEntityObject[] = []) {
    if (isNullOrUndefined(objs) || objs.length === 0) {
      return;
    }
    objs.forEach(obj => {
      this.expandedKeys.push(obj.ID);
      this.expandAll(obj.ContainChildObjects as GSPBizEntityObject[]);
    });
  }

  /**
   * 获取vmObjects的ID列表
   * @param objs
   */
  private getVmObjectsMappingIDs(objs: GSPViewObject[] = []) {
    if (isNullOrUndefined(objs) || objs.length === 0) {
      return;
    }
    objs.forEach(obj => {
      this.currentVmKeys.push(obj.Mapping.TargetObjId);
      this.checkedKeys.push(obj.Mapping.TargetObjId);
      this.getVmObjectsMappingIDs(obj.ContainChildObjects);
    });
  }
  /**
   * 加载子节点
   * @param node
   */
  public fetchChildren(node: GSPBizEntityObject): Observable<any[]> {
    return of(node.ContainChildObjects); // 若为对象节点，则返回子节点集合；
  }

  /**
   * 判断是否包含子节点
   * @param node
   */
  public hasChildren(node: GSPBizEntityObject): boolean {
    return node.ContainChildObjects && node.ContainChildObjects.length > 0;
  }

  /**
   * 重置
   */
  reset(): void {
    this.checkedKeys = [].concat(this.currentVmKeys);
  }

  public onConfirm(e): void {
    e.preventDefault();
    // 将vm映射对象列表拼接到checkedKeys
    this.checkedKeys = this.checkedKeys.concat(this.currentVmKeys);

    if (this.checkedKeys.length === this.currentVmKeys.length) {
      this.closeForm();
      return;
    } else {
      this.mappingService.addObjects(this.checkedKeys, this.vm);
    }
    this.closeForm();
  }

  public onCancel(e): void {
    e.preventDefault();
    this.closeForm();
  }

  private closeForm(): void {
    this.clear();
    this.active = false;
  }

  private clear() {
    this.checkedKeys = [];
    this.expandedKeys = [];
    this.currentVmKeys = [];
  }

  getDisplay(item: GSPBizEntityObject): string {
    if (this.containsInVmObjects(item.ID)) {
      return item.Name + '(vm)';
    } else {
      return item.Name;
    }
  }

  containsInVmObjects(id): boolean {
    return this.currentVmKeys.indexOf(id) > -1;
  }
}



