import {
  GSPViewModelElement, GSPBizEntityObject, ViewElementConst, GSPElementDataType,
  ViewObject, GSPViewObject, GSPElementObjectTypes, GSPElementDataTypes, ViewElement, GSPElementObjectType
} from './libs';
import { isNullOrUndefined } from 'util';
import { VmElementEditUtil } from './vm-element-edit-util';


export class VmElementUpdateUtil {
  static currentObject: GSPViewObject;
  static viewObject: ViewObject;
  static bizObject: GSPBizEntityObject;

  private static dataTypes = GSPElementDataTypes;

  static get currentViewElement(): ViewElement {
    return this.viewObject.elements.currentSelectItem;
  }


  /**
   * 字段属性是否可赋值
   * 1. be带出字段的[长度][精度]（可改小不可改大）
   */
  static CanUpdateElementProperty(propertyName: string, dataItem: GSPViewModelElement, newValue: any): boolean {
    // vm虚拟字段，可更改长度精度
    if (dataItem.IsVirtualViewElement) {
      return true;
    }
    // be字段，根据对应的be字段判断是否可更改
    switch (propertyName) {
      case ViewElementConst.Length:
        return this.canUpdateElementLength(this.bizObject, dataItem, newValue);
      case ViewElementConst.Precision:
        return this.canUpdateElementPrecision(this.bizObject, dataItem, newValue);
      default:
        return true;
    }
  }

  /**
   * 是否可更新字段长度
   * @param bizObj be对象
   * @param dataItem vm字段
   * @param newLength 长度新值
   */
  private static canUpdateElementLength(bizObj: GSPBizEntityObject, dataItem: GSPViewModelElement, newLength: number): boolean {
    const bizEleId = dataItem.Mapping.TargetObjId;
    const bizElement = bizObj.ContainElements.find(bizEle => bizEle.ID === bizEleId);
    const oldLength = bizElement.Length;
    if (oldLength < newLength) {
      alert(`字段长度不能大于对应的业务实体的字段长度'${oldLength}'。`);
      return false;
    } else {
      return true;
    }
  }

  /**
   * 是否可更新字段长度
   * @param bizObj be对象
   * @param dataItem vm字段
   * @param newPrecision 长度新值
   */
  private static canUpdateElementPrecision(bizObj: GSPBizEntityObject, dataItem: GSPViewModelElement, newPrecision: number): boolean {
    const bizEleId = dataItem.Mapping.TargetObjId;
    const bizElement = bizObj.ContainElements.find(bizEle => bizEle.ID === bizEleId);
    const oldPrecision = bizElement.Precision;
    if (oldPrecision < newPrecision) {
      alert(`字段精度不能大于映射业务实体字段的精度'${oldPrecision}'。`);
      return false;
    } else {
      return true;
    }
  }

  static updateCurrentProperty(propertyName: string, dataItem: GSPViewModelElement, newValue: any): any {
    // 业务字段属性相关
    if (propertyName === ViewElementConst.MDataType) {
      if (newValue === 'UnifiedDataType') {
        // ①启用[业务字段]，不需要设置其他属性，return
        this.currentViewElement.set(ViewElementConst.IsUdt, true);
        return;

      } else if ((<GSPViewModelElement>dataItem).IsUdt) {
        // ②关闭[业务字段],需设置数据类型及其相关的属性，不return
        this.viewObject.elements.updateByID((<GSPViewModelElement>dataItem).ID, oldValue => {
          const ele = oldValue.toJS() as GSPViewModelElement;
          ele.IsUdt = false;
          // ele.UnifiedDataType = null;
          ele.UdtName = '';
          ele.UdtPkgName = '';
          ele.UdtID = '';
          return new ViewElement(ele);
        });
      }
    }
    // ③ 允许修改属性
    this.updateValueByElementId(dataItem.ID, propertyName, newValue);
  }


  /**
   * 处理相关的属性[是否多语、关联枚举、标签]
   * @param propertyName 属性名
   * @param dataItem 当前字段
   * @param newValue 属性值
   */
  static updateRelativeProperty(propertyName: string, dataItem: GSPViewModelElement, newValue: any) {
    // ① 处理是否多语，若当前是否多语为是，修改数据类型/对象类型，将影响是否多语属性值
    const isMultiLanguage = this.getViewElementById(dataItem.ID).get(ViewElementConst.IsMultiLanguage);
    if (isMultiLanguage) {
      // 1-1 切换字段数据类型，若不是文本类型，则设置是否多语为否
      if (propertyName === ViewElementConst.MDataType) {
        if (newValue !== GSPElementDataType.String && newValue !== GSPElementDataType.Text) {
          this.setIsMultiLanguage(dataItem.ID, false);
        }
      }
      // 1-2 切换字段对象类型，若为关联类型，则设置是否多语为否
      if (propertyName === ViewElementConst.ObjectType) {
        if (newValue === GSPElementObjectType.Association) {
          this.setIsMultiLanguage(dataItem.ID, false);
        }
      }
    }

    // ② 处理关联/枚举
    // 切换对象类型时，清空相应的枚举/关联信息
    if (propertyName === ViewElementConst.ObjectType) {
      if (newValue !== GSPElementObjectType.Association) {
        this.viewObject.elements.updateByID(dataItem.ID, elementView => {
          (<ViewElement>elementView).clearAssociations();
          return elementView;
        });
      }
      if (newValue !== GSPElementObjectType.Enum) {

        this.viewObject.elements.updateByID(dataItem.ID, elementView => {
          (<ViewElement>elementView).clearEnumValues();
          return elementView;
        });
      }

      this.changeLengthPrecisionByObjectType(newValue, dataItem);
    }

    // ③ 若标签为空，自动填充编号
    // 修改编号值时，判断若标签为空，则将标签赋同样的值
    if (propertyName === ViewElementConst.Code && newValue.length > 0) {
      const currentElement = this.viewObject.elements.getItemByID(dataItem.ID) as ViewElement;
      const labelID = currentElement.get(ViewElementConst.LabelID) as string;
      if (isNullOrUndefined(labelID) || labelID.length === 0) {
        currentElement.update(ViewElementConst.LabelID, () => newValue);
      }
    }

    // ④ 若调整数据类型，则长度精度随之改变
    if (propertyName === ViewElementConst.MDataType) {
      this.changeLengthPrecisionByType(newValue, dataItem);
    }

    // ⑤ 默认值相关：若对象类型改为关联，或者当前字段设置为多语，则清空默认值
    if (propertyName === ViewElementConst.ObjectType && newValue === GSPElementObjectType.Association) {
      this.clearDefaultValueByID(dataItem.ID);
    }
    if (propertyName === ViewElementConst.IsMultiLanguage && newValue === true) {
      this.clearDefaultValueByID(dataItem.ID);
    }
  }

  /**
   * 切换数据类型时，带出长度精度
   */
  static changeLengthPrecisionByType(type: GSPElementDataType, dataItem: GSPViewModelElement) {
    if (type !== dataItem.MDataType) {
      const length = this.dataTypes.find(item => item.value === type).length;
      const precision = this.dataTypes.find(item => item.value === type).precision;
      this.updateValueByElementId(dataItem.ID, ViewElementConst.Length, length);
      this.updateValueByElementId(dataItem.ID, ViewElementConst.Precision, precision);
    }
  }

  /**
   * 根据字段ID更新属性值
   * @param elementId 字段ID
   * @param propertyName 属性名
   * @param propertyValue 属性值
   */
  static updateValueByElementId(elementId: string, propertyName: string, propertyValue: any) {
    this.viewObject.elements.updateByID(elementId, elementView => {
      elementView.update(propertyName, () => propertyValue);
      return elementView;
    });
  }

  /**
   * 关联字段展示部分的数据源
   * @param id 字段ID
   */
  static getViewElementById(id: string) {
    return this.viewObject.elements.getItemByID(id);
  }


  /**
   * 清空默认值
   * @param id
   */
  static clearDefaultValueByID(id: string) {
    this.updateValueByElementId(id, ViewElementConst.DefaultValue, '');
  }

  /**
   * 切换对象类型为关联时，带出长度36精度0
   * @param type 当前对象类型
   * @param dataItem 当前字段
   */
  static changeLengthPrecisionByObjectType(type: GSPElementObjectType, dataItem: GSPViewModelElement) {
    if (type === GSPElementObjectType.Association) {
      this.viewObject.elements.updateByID(dataItem.ID, oldEle => {
        const ele = oldEle.toJS() as GSPViewModelElement;
        ele.MDataType = GSPElementDataType.String;
        ele.Length = 36;
        ele.Precision = 0;
        return new ViewElement(ele);
      });
    }
  }

  /**
   * 修改[是否多语]复选框状态
   * @param newValue 是否多语
   * @param dataItem 当前字段
   */
  static changeIsMultiLanguageCheck(newValue: boolean, dataItem: GSPViewModelElement) {
    if (!this.canEditIsMultiLanguage(dataItem, newValue)) {
      return;
    }
    this.updateCurrentProperty(ViewElementConst.IsMultiLanguage, dataItem, newValue);
    this.updateRelativeProperty(ViewElementConst.IsMultiLanguage, dataItem, true);
  }

  /**
   * 以下情况不允许多语：
   * 1.字段类型非[文本、备注]；
   * 2.ID类型的字段，不允许多语，包括ID,ParentID,关联字段；
   * 3.20180917_be带出字段，不可编辑；而vm自增字段，是虚拟字段，不对应表结构，不存在多语一说；综上，均为不可编辑，且需要手动将值还原;
   */
  private static canEditIsMultiLanguage(dataItem: GSPViewModelElement, newValue: boolean): boolean {
    this.setIsMultiLanguage(dataItem.ID, !newValue);
    return false;
    // if (newValue) {
    //   if (dataItem.MDataType !== GSPElementDataType.String && dataItem.MDataType !== GSPElementDataType.Text) {
    //     // alert('数据类型为[文本]或[备注]时，可设置为多语。');
    //     this.setIsMultiLanguageFalse(dataItem.ID);
    //     return false;
    //   }
    //   if (VmElementEditUtil.isIDType(dataItem)) {
    //     // alert('ID类型的字段不允许设置为多语，包括ID,ParentID,关联字段。');
    //     this.setIsMultiLanguageFalse(dataItem.ID);
    //     return false;
    //   }
    // }
    // return true;
  }

  /**
   * 设置是否多语属性为否
   * @param elementId 字段ID
   */
  private static setIsMultiLanguage(elementId: string, value: boolean) {
    this.updateValueByElementId(elementId, ViewElementConst.IsMultiLanguage, value);
  }
}



