import { Component, ViewChild, Input } from '@angular/core';
import {
  BizMgrAction, ViewModelAction, MappedBizAction, ViewMappedBizAction,
  ViewMappedCdpAction, MappedCdpAction, ViewModelActionType, Guid, ViewModelMapping
} from '../util/vm-reference';
import { BaseOperationComponentComponent } from '../base-operation-component/base-operation-component.component';
import { isNullOrUndefined } from 'util';
import { BeMgrActionsGridComponent } from './be-mgr-actions-grid/be-mgr-actions-grid.component';
import { VmMappingService } from '../service/vm-mapping.service';

@Component({
  selector: 'app-vm-action',
  templateUrl: './vm-action.component.html',
  styleUrls: ['./vm-action.component.css'],
})
export class VmActionComponent extends BaseOperationComponentComponent {

  /**
   * 列表数据源
   */
  @Input() mapping: ViewModelMapping;
  @ViewChild('editActions') addActionsFromBe: BeMgrActionsGridComponent;

  constructor() {
    super();
  }

  get isBizAction(): boolean {
    if (isNullOrUndefined(this.viewOperation)) {
      return true;
    }
    return this.viewOperation.type === ViewModelActionType.BEAction;
  }

  /**
   * be带出操作的添加，应为弹窗表格里选
   */
  addBizMgrAction() {
    this.addActionsFromBe.load(this.viewOperations ? this.viewOperations.toJS() : null, this.mapping);
  }

  /**
   * 新增vmAction
   */
  addCdpAction() {
    const newAction = new MappedCdpAction();
    this.viewOperations.add(new ViewMappedCdpAction(newAction));
    this.viewOperations.setSelectIndexByID(newAction.ID);
    this.refresh();
  }

  /**
   * 新增be映射操作[确认]
   */
  getNewViewElements(actions: MappedBizAction[]) {
    if (!isNullOrUndefined(actions) && actions.length > 0) {
      actions.forEach(action => {
        this.viewOperations.add(new ViewMappedBizAction(action));
      });
      this.refresh();
    }
  }
}



