import { Component, OnInit, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { AuthData } from './util/auth-data';
import {
  ViewModel, ViewModelAction, ViewModelActionType, MappedBizAction, GSPMetadataService,
  MetadataDto, GSPBusinessEntity, GSPViewModel, BizMgrAction
} from '../../util/vm-reference';
import { VmMetadataService } from '../../service/vm-metadata.service';

import { ConvertToAuthDatas } from './util/convert-to-authdatas';
import { AuthOpTabComponent } from './auth-op-tab/auth-op-tab.component';
import { FuncOpTabComponent } from './func-op-tab/func-op-tab.component';
import { SecurityEntryEntity } from './security-entry/index';
import { SecurityEntryService } from './security-entry/service/security-entry.service';
@Component({
  selector: 'app-auth-tab',
  templateUrl: './auth-tab.component.html',
  styleUrls: ['./auth-tab.component.css'],
  providers: [VmMetadataService]
})
export class AuthTabComponent implements OnInit, OnChanges {

  @ViewChild('funcOpTab') funcOpTab: FuncOpTabComponent;
  @ViewChild('authOpTab') authOpTab: AuthOpTabComponent;

  @Input() viewModel: ViewModel;
  @Input() securityFuncEntry: SecurityEntryEntity;
  @Input() securityAuthEntry: SecurityEntryEntity;

  tabNameFuncOp = '操作权限';
  tabNameAuthOp = '数据权限';
  extTypeVM = 'VM';

  currentTabTitle = this.tabNameFuncOp;
  /**
   * 操作权限数据源
   */
  funcOpActions: AuthData[];

  /**
   * 数据权限数据源
   */
  authOpElements: AuthData[];
  authOpActions: AuthData[];

  constructor(private metadataService: VmMetadataService, private securityEntryService:  SecurityEntryService) { }

  ngOnInit() {
    this.refresh();
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.refresh();
  }

  refresh() {
    // funcOp
    const vm = this.viewModel.toJS();
    if (isNullOrUndefined(this.securityFuncEntry)) {
      this.securityFuncEntry = this.getFuncOpSecurityEntry(vm);
    }

    if (isNullOrUndefined(this.securityAuthEntry)) {
      this.securityAuthEntry = this.getFuncOpSecurityEntry(vm);
    }

    const beId = vm.Mapping.TargetMetadataId;
    this.metadataService.getRefBeMetadata(beId).subscribe(
      beMetadataDto => {
        const dto = beMetadataDto as MetadataDto;
        const be = JSON.parse(dto.content) as GSPBusinessEntity;
        this.getFuncOpActions(be);

        // authOp
        this.getAuthOpActions(be);
        this.getAuthOpElements(be);
      }
    );
  }

  getFuncOpSecurityEntry(vm: GSPViewModel): SecurityEntryEntity {
    const entity = new SecurityEntryEntity();
    entity.ex1 = vm.ID;
    entity.extType = this.extTypeVM;
    return entity;
  }

  getFuncOpActions(be: GSPBusinessEntity) {
    const mgrActions = be.BizMgrActions;
    this.funcOpActions = ConvertToAuthDatas.convertMgrActionsToAuthDatas(mgrActions);
  }

  /**
   * 数据权限传递BeActions
   */
  getAuthOpActions(be: GSPBusinessEntity) {
    const bizActions = be.MainObject.BizActions;
    this.authOpActions = ConvertToAuthDatas.convertMgrActionsToAuthDatas(bizActions);
  }

  /**
   * 数据权限传递BeElements
   */
  getAuthOpElements(be: GSPBusinessEntity) {
    const elements = be.MainObject.ContainElements;
    this.authOpElements = ConvertToAuthDatas.convertElementsToFuncOpActions(elements);
  }

  save() {
    this.securityEntryService.saveFuncSecurityEntry(this.securityFuncEntry).subscribe();
    this.securityEntryService.saveAuthSecurityEntry(this.securityAuthEntry).subscribe();
  }

}



