import { Map } from 'immutable';

import { MapBase } from '../../base/mapBase';

import {
    MappedBizActionParameter, MappedCdpActionParameter, BizParameterType, BizParameterMode,
    BizParameterBase, ViewOperationConst, ViewModelParameter
} from '../../../ref';

export abstract class ViewParameterBase extends MapBase {

    /**
     * 构造函数
     * @param parameter
     */
    constructor(parameter: ViewModelParameter | Map<string, any>) {
        super(parameter);
    }

    get ParamCode(): string {
        return this.get(ViewOperationConst.ParamCode);
    }
    set ParamCode(value: string) {
        this.set(ViewOperationConst.ParamCode, value);
    }

    get ParamName(): string {
        return this.get(ViewOperationConst.ParamName);
    }
    set ParamName(value: string) {
        this.set(ViewOperationConst.ParamName, value);
    }

    get ParameterType(): BizParameterType {
        return this.get(ViewOperationConst.ParameterType);
    }
    set ParameterType(value: BizParameterType) {
        this.set(ViewOperationConst.ParameterType, value);
    }

    get Assembly(): string {
        return this.get(ViewOperationConst.Assembly);
    }
    set Assembly(value: string) {
        this.set(ViewOperationConst.Assembly, value);
    }

    get ClassName(): string {
        return this.get(ViewOperationConst.ClassName);
    }
    set ClassName(value: string) {
        this.set(ViewOperationConst.ClassName, value);
    }

    get Mode(): BizParameterMode {
        return this.get(ViewOperationConst.Mode);
    }
    set Mode(value: BizParameterMode) {
        this.set(ViewOperationConst.Mode, value);
    }

    get ParamDescription(): string {
        return this.get(ViewOperationConst.ParamDescription);
    }
    set ParamDescription(value: string) {
        this.set(ViewOperationConst.ParamDescription, value);
    }
}

export class ViewBizActionParameter extends ViewParameterBase {
    constructor(parameter: MappedBizActionParameter | Map<string, any>) {
        super(parameter);
    }
}

export class ViewBizMgrActionParameter extends ViewParameterBase {
    constructor(parameter: MappedCdpActionParameter | Map<string, any>) {
        super(parameter);
    }
}



