import { List, Map } from 'immutable';
import { isNullOrUndefined } from 'util';
import { Subject } from 'rxjs/Subject';

import { ViewAssoKeyList } from './viewAssoKeyList';
import { ListBase } from '../base/listBase';
import { MapBase } from '../base/mapBase';
import { ViewElement } from '../viewElement/viewElement';

import { IGSPCommonElement, ViewElementConst } from '../../ref';


export class ViewAssociation extends MapBase {

    /**
     * 构造函数
     * @param element
     */
    constructor(asso: any) {
        super(asso);
        if (isNullOrUndefined(asso)) {
            return;
        }
        this.data = this.getViewAssociation(this.data);
    }

    private getViewAssociation(asso: Map<string, any>): Map<string, any> {
        const viewAssociation = asso.update(ViewElementConst.KeyCollection, keys => this.getViewAssoKeys(keys))
            .update(ViewElementConst.RefElementCollection, refElements => this.getRefElements(refElements));
        return viewAssociation;
    }

    private getViewAssoKeys(keys: List<any>): ViewAssoKeyList {
        return new ViewAssoKeyList(keys);
    }

    private getRefElements(refElements: List<any>): ListBase<MapBase> {
        return new RefViewElementList(refElements);
    }

    public set belongElement(value: ViewElement) {
        this.update(ViewElementConst.BelongElement, value);
    }

    public get belongElement(): ViewElement {
        return this.get(ViewElementConst.BelongElement);
    }

    /**
     * 当前关联键值对
     */
    get viewAssoKeys(): ViewAssoKeyList {
        const assoKeys = this.get(ViewElementConst.KeyCollection) as ViewAssoKeyList;
        // assoKeys.refreshView.subscribe(item => {
        //     this.refreshViewLayer();
        // });
        return assoKeys;
    }
    /**
     * 当前关联键值对
     */
    set viewAssoKeys(value: ViewAssoKeyList) {
        this.set(ViewElementConst.KeyCollection, value);
    }
    set viewRefElements(value: RefViewElementList) {
        this.set(ViewElementConst.RefElementCollection, value);
    }

    /**
     * 当前关联键值对
     */
    get viewRefElements(): RefViewElementList {
        const refElements = this.get(ViewElementConst.RefElementCollection) as RefViewElementList;
        // refElements.refreshView.subscribe(item => {
        //     this.refreshViewLayer();
        // });
        return refElements;
    }

    get RefModelID(): string {
        return this.get(ViewElementConst.RefModelID);
    }

    set RefModelID(id: string) {
        this.set(ViewElementConst.RefModelID, id);
    }

    get RefModelPkgName(): string {
        return this.get(ViewElementConst.RefModelPkgName);
    }

    set RefModelPkgName(pkgName: string) {
        this.set(ViewElementConst.RefModelPkgName, pkgName);
    }

    get RefModelCode(): string {
        return this.get(ViewElementConst.RefModelCode);
    }

    set RefModelCode(id: string) {
        this.set(ViewElementConst.RefModelCode, id);
    }

    get RefModelName(): string {
        return this.get(ViewElementConst.RefModelName);
    }

    set RefModelName(name: string) {
        this.set(ViewElementConst.RefModelName, name);
    }
}

export class RefViewElementList extends ListBase<RefViewElement> {

    confirmRefElements: Subject<this> = new Subject<this>();
    constructor(elements: IGSPCommonElement[] | List<any>) {
        super(elements);
        if (isNullOrUndefined(elements)) {
            return;
        }
        this.data = this.getViewElements(this.data);
    }

    getViewElements(elementArray: List<any>): any {
        if (isNullOrUndefined(elementArray)) {
            return;
        }

        // let viewElementList = List<any>(elementArray);
        let viewElementList = elementArray;

        if (elementArray.size > 0) {
            for (let i = 0; i < elementArray.size; i++) {
                viewElementList = viewElementList.update(i, element => new RefViewElement(element));
            }
            return viewElementList;
        }
    }
}

export class RefViewElement extends MapBase {
    constructor(element: Map<string, any>) {
        super(element);
    }
}



