import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { FuncOperationEntity } from '../../entity/func-operation.entity';
import { isNullOrUndefined, isNull } from 'util';
import { SecurityEntryService } from '../../service/security-entry.service';

@Component({
  selector: 'app-func-op-select',
  templateUrl: './func-op-select.component.html',
  styleUrls: ['./func-op-select.component.css']
})
export class FuncOpSelectComponent implements OnInit, OnChanges {

  closeModalEmitter: EventEmitter<any> = new EventEmitter<FuncOperationEntity>();
  selectOperationEmitter: EventEmitter<FuncOperationEntity> = new EventEmitter<FuncOperationEntity>();
  operationSelected: FuncOperationEntity;
  ops: FuncOperationEntity[] = [];
  expandedKeys: string[] = [];

  constructor(private securityEntryervice: SecurityEntryService) { }

  ngOnInit() {
    this.securityEntryervice.getFuncOps().subscribe(datas => {
      datas.forEach(item => {
        if (item.children && item.children.length > 0) {
          this.ops.push(item);
        }
      });
    });
  }

  public hasChildren = (item: any) => item.children && item.children.length > 0;
  public fetchChildren = (item: any) => of(item.children);

  ngOnChanges(): void {
  }

  hideModal(): void {
    this.closeModalEmitter.next();
    this.selectOperationEmitter.next(this.operationSelected);
  }

  // 弹出框 分配的功能操作控件特性
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'single'
    };
  }

  // 弹出框 分配的功能操作控件图标样式
  iconClass({ type }: any): any {
    return {
      'k-i-folder': type.toString() !== '1',
      'k-i-gear': type.toString() === '1',
      'k-icon': true
    };
  }

  btnOkClick() {
    this.hideModal();
  }

  btnClearClick() {
    this.operationSelected = null;
    this.hideModal();
  }

  // 切换事件
  handleSelection({ dataItem }: any): void {
    this.operationSelected = dataItem;
  }
}




