import { Component, OnInit, ViewChild, Input, Output, OnChanges, SimpleChange, EventEmitter, SimpleChanges } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { isNullOrUndefined, isNull } from 'util';

import { AuthoperationEntity } from '../../../../authorization/entity/authoperationentity';
import { AuthorizationService } from '../../../../authorization/service/authorization.service';

import { SecurityEntryService } from '../../../service/security-entry.service';


@Component({
  selector: 'app-auth-op-select',
  templateUrl: './auth-op-select.component.html',
  styleUrls: ['./auth-op-select.component.css']
})
export class AuthOpSelectComponent implements OnInit {

  authorizationId: string;
  authfield: string;

  closeModalEmitter: EventEmitter<any> = new EventEmitter<AuthoperationEntity>();
  selectOperationEmitter: EventEmitter<AuthoperationEntity> = new EventEmitter<AuthoperationEntity>();
  operationSelected: AuthoperationEntity;
  ops: AuthoperationEntity[] = [];
  expandedKeys: string[] = [];

  constructor(
    private authorizationService: AuthorizationService,
    private securityEntryervice: SecurityEntryService) { }

  ngOnInit() {
    this.authorizationService.GetAuthOperations(this.authorizationId, this.authfield).subscribe(datas => {
      datas.forEach(item => this.ops.push(item));
    });
  }

  public hasChildren = (item: any) => item.children && item.children.length > 0;
  public fetchChildren = (item: any) => of(item.children);

  hideModal(): void {
    this.closeModalEmitter.next();
    this.selectOperationEmitter.next(this.operationSelected);
  }

  // 弹出框 分配的功能操作控件特性
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'single'
    };
  }

  // 弹出框 分配的功能操作控件图标样式
  iconClass({ type }: any): any {
    return {
      'k-i-gear': true,
      'k-icon': true
    };
  }

  btnOkClick() {
    this.hideModal();
  }

  btnClearClick() {
    this.operationSelected = null;
    this.hideModal();
  }

  // 切换事件
  handleSelection({ dataItem }: any): void {
    this.operationSelected = dataItem;
  }
}




